<template>
  <div :class="['article-pickup', (isNoData || isLoadingFail) ? 'specialHeightCol' : '']">
    <div class="pickupTitle">
      <span>{{title}}</span>
    </div>
    <div class="pickupSort">
      <span :class="activeTab == 'day' ? 'active' : '' " @click="switchTab('day')" v-if="tabDayVisabled" >昨日</span>
      <div class="split" v-if="tabDayVisabled" ></div>
      <span :class="activeTab == 'week' ? 'active' : '' " @click="switchTab('week')">週間</span>
      <div class="split"></div>
      <span :class="activeTab == 'comment' ? 'active' : '' " @click="switchTab('comment')">コメント数</span>
    </div>
    <div class="pickupArticleList">
      <pickUp-article :isLast="index == (articles.length - 1)" :isLifeStyleQA="menuId == Global.MENU_ID_LIFESTYLE_QA" :article="item" :sortNo="index + 1" v-for="(item, index) in articles" :key="index" @setListScrollTop="setListScrollTop"/>
      <div :class="['special', isNoData? 'left' : '']" v-if="(isNoData || isLoadingFail) && !isLoading">
        <div class="clickArea" @click="reLoadData">
          <div class="refresh" v-if="isLoadingFail">
            <img src="@/assets/medyImages/refresh-small.png" srcset="@/assets/medyImages/refresh-small@2x.png 2x, @/assets/medyImages/refresh-small@3x.png 3x">
          </div>
          <span>{{specialText}}</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import PickUpArticle from "@/components/cmptsCommon/PickUpArticle.vue"
import apiParam704001 from "@/components/apiParam/IF704001.vue"

export default {
  name: 'pickUpArticles',
  props: {
    menuId: Number
  },
  data() {
    return {
      activeTab: 'day',
      articles: [],
      title: '',
      isLoading: false,
      isLoadingFail: false,
      isNoData: false,
      tabDayVisabled:true,
      param704001:apiParam704001,
      loaded:null,
      specialText: 'コンテンツがありません。',
      fakeArticles: [
        {
          articleId: "310001",
          articleImg: "https://s3-ap-northeast-1.amazonaws.com/image-picker-static/images/20160617015843/Fotolia_67955160_Subscription_Monthly_M.jpg",
          articleTitle: "タイトルタイトルタイトルタイトルタイトルタイトルタイトル最大２ラインまで",
          commentCount: "12",
          articleTypeName: '人間関係',
        },
        {
          articleId: "310002",
          articleImg: "https://s3-ap-northeast-1.amazonaws.com/image-picker-static/images/20160617015843/Fotolia_67955160_Subscription_Monthly_M.jpg",
          articleTitle: "タイトルタイトルタイトルタイトルタイトルタイトルタイトル最大２ラインまで",
          commentCount: "12",
          articleTypeName: '住まいと暮らし',
        },
        {
          articleId: "310002",
          articleImg: "https://s3-ap-northeast-1.amazonaws.com/image-picker-static/images/20160617015843/Fotolia_67955160_Subscription_Monthly_M.jpg",
          articleTitle: "タイトルタイトルタイトルタイトルタイトルタイトルタイトル最大２ラインまで",
          commentCount: "12",
          articleTypeName: '人間関係',
        },
        {
          articleId: "310002",
          articleImg: "https://s3-ap-northeast-1.amazonaws.com/image-picker-static/images/20160617015843/Fotolia_67955160_Subscription_Monthly_M.jpg",
          articleTitle: "タイトルタイトルタイトルタイトルタイトルタイトルタイトル最大２ラインまで",
          commentCount: "12",
          articleTypeName: '住まいと暮らし',
        },
        {
          articleId: "310008",
          articleImg: "https://s3-ap-northeast-1.amazonaws.com/image-picker-static/images/20160617015843/Fotolia_67955160_Subscription_Monthly_M.jpg",
          articleTitle: "コメントできない記事",
          commentCount: "12",
          articleTypeName: '人間関係',
        }
      ]
    };
  },
  components: {
    "pickUp-article": PickUpArticle
  },
  mounted: function () {
    this.init();
  },
  activated: function () {
    if (this.$store.state.isPageBack) {
      // gobackの場合、keep-alliveでcacheを利用する、画面をrefreshしない
    } else {
      // 普通遷移の場合、画面初期化処理を行う
      this.init();
    }
  },
  methods: {
    init: function () {
      //TODO AJAX
      var _this = this;
      _this.articles = [];
      _this.isNoData = false;
      _this.isLoadingFail = false;
      _this.specialText = 'コンテンツがありません。';
      _this.tabDayVisabled = true;
      _this.activeTab = 'day';
      if(_this.menuId == _this.Global.MENU_ID_CLINIC){
        _this.title='臨床Q&A PICK UP';
      } else if(_this.menuId == _this.Global.MENU_ID_NEWS){
        _this.title='医療ニュースランキング';
      } else if(_this.menuId == _this.Global.MENU_ID_LIFESTYLE_QA){
        _this.title='今注目のライフスタイルQ&A';
      } else if(_this.menuId == _this.Global.MENU_ID_FEATURE){
        _this.title='特集・企画 PICK UP';
        _this.activeTab = 'week';
        _this.tabDayVisabled = false;
      } else if (_this.menuId == _this.Global.MENU_ID_ENQUETE) {
        _this.title='アンケート結果　注目ランキング';
        _this.activeTab = 'week';
        _this.tabDayVisabled = false;
      }
      this.getArticleByTab();
    },
    setListScrollTop: function () {
      // pulltoの場合、scrolltopを銘記
      this.$emit("setListScrollTop");
    },
    switchTab: function (chooseTab) {
      // タブを切り替え
      if (this.isLoading || this.activeTab == chooseTab) {
        return;
      }
      this.isLoading = true;
      if(chooseTab == this.activeTab){
        return;
      }
      if (chooseTab == 'day' && this.activeTab != 'day') {
        this.activeTab = 'day';
      } else if (chooseTab == 'week' && this.activeTab != 'week') {
        this.activeTab = 'week';
      } else if (chooseTab == 'comment' && this.activeTab != 'comment') {
        this.activeTab = 'comment';
      }
      this.getArticleByTab();
    },
    getFakerArticleByTab: function(loaded = null){
      var _this = this;
      if(loaded == null){
        _this.articles = [];
      }
      //TODO AJAX
      setTimeout(function () {
        //TODO DELETE
         if(_this.activeTab == 'week'){
            _this.specialText = 'コンテンツがありません。';
            _this.isNoData = true;
            _this.isLoadingFail = false;
            // TODO FEATURE TEST
            if(_this.menuId == _this.Global.MENU_ID_FEATURE){
              _this.isNoData = false;
              _this.isLoadingFail = false;
              _this.articles = _this.fakeArticles;
            } else if (_this.menuId == _this.Global.MENU_ID_ENQUETE) {
              _this.isNoData = false;
              _this.isLoadingFail = false;
              _this.articles = _this.fakeArticles;
            }
        } else if(_this.activeTab == 'day'){
          _this.isNoData = false;
          _this.isLoadingFail = false;
          _this.articles = _this.fakeArticles;
        } else {
          _this.specialText = '再読み込み';
          _this.isLoadingFail = true;
          _this.isNoData = false;
        }
        _this.isLoading = false;
      }, 1000);
    },
    doIf704001Success: function(response){
      var _this = this;
      if (response.data != null && response.data.result == "0000") {
        _this.isLoadingFail = false;
        var data = response.data;

        // 記事データがない場合
        if ((data.articleList == undefined) || (data.articleList.length == 0)) {
            _this.specialText = 'コンテンツがありません。';
            _this.isNoData = true;
        } else {
            // リーフラッシュする場合、記事リストを初期化する
            if (_this.loaded != null) {
              _this.articles = [];
            }
            _this.isNoData = false;
            for (var i=0; i < data.articleList.length; i++) {
              var article = {
                    articleId: data.articleList[i].articleId,
                    articleImg: data.articleList[i].externalLinkImg,
                    articleTitle: data.articleList[i].articleTitle,
                    commentCount: "",
                    articleTypeName: '',
                  }
              
              if (typeof (data.articleList[i].externalLinkImg) != "undefined" && data.articleList[i].externalLinkImg.slice(0,5) == "http:") {
                article.articleImg = "https" + data.articleList[i].externalLinkImg.substr(4);
              }

              _this.articles.push(article);
            }

            // TODO GA送信を実施

            if(_this.loaded !== null){
              loaded('done');
            }
        }
      } else {
        // APIレスポンスからエラーが発生する場合
        _this.isLoadingFail = true;
        _this.specialText = '再読み込み';
        if (_this.loaded != null) {
          loaded("fail");
        }
      }
    },
    doIf704001Fail: function(){
      var _this = this;
      _this.specialText = '再読み込み';
      _this.isLoadingFail = true;
      _this.isNoData = false;
    },
    doIf704001Finally: function(){
    },
    getArticleByTab: function (loaded = null) {
      var _this = this;
      // TODO FOR DEVELOP
      if (_this.Global.ENV_FLAG == "develop") {
        _this.getFakerArticleByTab();
        return;
      }

      this.loaded = loaded;

      if (this.activeTab == 'day') {
        this.param704001.param.rankingType = _this.Global.RANKING_TYPE_YESTERDAY
      } else if (this.activeTab == 'week') {
        this.param704001.param.rankingType = _this.Global.RANKING_TYPE_WEEK
      } else if (this.activeTab == 'comment') {
        this.param704001.param.rankingType = _this.Global.RANKING_TYPE_COMMENT
      }
      this.param704001.param.menuId = _this.menuId;

      var finallyMethod = new Function(_this.doIf704001Finally("hello world"));
      _this.APILoader.apiDoGet(this.param704001.URL,this.param704001.getParamStr(),
        _this.doIf704001Success,_this.doIf704001Fail,finallyMethod);
    },
    reLoadData: function(){
      if(!this.isLoadingFail){
        return;
      }
      this.isLoading = true;
      this.getArticleByTab();
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.article-pickup {
  width: 100%;
  min-height: 454px;
  background-color: #fafafa;
}

.article-pickup.specialHeightCol{
  height: auto;
  min-height: 0px;
}

.article-pickup .pickupTitle {
  padding-top: 20px;
  padding-left: 0px;
  height: 36px;
  line-height: 36px;
}

.article-pickup .pickupTitle span {
  padding-top: 0px;
  padding-left: 12px;
  font-size: 18px;
  font-weight: 500;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: normal;
  color: var(--black);
  height: 27px;
}

.article-pickup .pickupSort {
  float: right;
  margin-right: 12px;
  height: 24px;
  margin-top: 5px;
}

.article-pickup .pickupSort span {
  margin-top: 2px;
  width: 60px;
  height: 18px;
  font-size: 12px;
  font-weight: 300;
  color: var(--brown-grey);
}

.article-pickup .pickupSort .split {
  width: 1px;
  height: 8px;
  background-color: var(--black);
  margin-left: 10px;
  margin-right: 10px;
  display: inline-block;
}

.article-pickup .pickupSort .active {
  color: var(--black);
}

.article-pickup .pickupArticleList {
  position: relative;
  display: inline-block;
  width: 100%;
  min-height: 52px;
  vertical-align: top;
}

.article-pickup .special{
  text-align: center;
  margin-top: 16px;
  margin-bottom: 16px;
  height: 20px;
}

.article-pickup .special.left{
  text-align: left;
  padding-left: 12px;
}

.article-pickup .special span{
  font-size: 14px;
  height: 20px;
  color: var(--black);
  line-height: 20px;
  vertical-align: top;
  display: inline-block;
}

.article-pickup .special .refresh{
  width: 16px;
  height: 16px;
  margin-right: 8px;
  padding-top: 2px;
  display: inline-block;
}

.article-pickup .special .clickArea{
  display: inline-block;
}
</style>
