<script type="text/javascript">

const MENU_ID_TOP = 100
const MENU_ID_CLINIC = 400
const MENU_ID_NEWS = 200
const MENU_ID_NEWS_OPINIONS = 210
const MENU_ID_NEWS_MYMEDICALNEWS = 220
const MENU_ID_ENQUETE = 600
const MENU_ID_FEATURE = 300
const MENU_ID_FEATURE_BREAKTIME = 310
const MENU_ID_FEATURE_DOCTOR_LIBRARY = 300
const MENU_ID_NAVI = 500
const MENU_ID_LIFE = 700
const MENU_ID_LIFESTYLE_INDEX = 710
const MENU_ID_LIFESTYLE_PRIVILEGE = 720
const MENU_ID_LIFESTYLE_FEATURE = 730
const MENU_ID_LIFESTYLE_QA = 740

const RANKING_TYPE_YESTERDAY = 0
const RANKING_TYPE_WEEK = 1
const RANKING_TYPE_COMMENT = 2
const LOGIN_PAGE_URL = 'https://www.ldmedy01.tk'
const MEDY_ARTICLE_DETAIL_URL = 'https://www.ldmedy01.tk/article/'

const ENV_FLAG = "develop"

var currentMenuId = MENU_ID_CLINIC

var subPageCount = 0;

var isPageBack = false;

var toggleFix = function (value) {
  if(value){
    document.getElementsByTagName('html')[0].classList.add("haveModal");
    if(typeof(document.getElementsByClassName('scroll-container')[0]) != "undefined"){
      document.getElementsByClassName('scroll-container')[0].style.overflow = 'hidden';
    }
    if(this.currentMenuId == MENU_ID_CLINIC &&  typeof(document.getElementsByClassName('topArea')[0])!="undefined" ){
      document.getElementsByClassName('topArea')[0].style.overflowX = 'hidden';
    }
    if(typeof(document.getElementsByClassName('userInfoScrollArea')[0])!="undefined" ){
      document.getElementsByClassName('userInfoScrollArea')[0].style.overflowX = 'hidden';
    }
  } else {
    document.getElementsByTagName('html')[0].classList.remove("haveModal");
    if(typeof(document.getElementsByClassName('scroll-container')[0]) != "undefined"){
      document.getElementsByClassName('scroll-container')[0].style.overflow = 'auto';
    }
    if(this.currentMenuId == MENU_ID_CLINIC && typeof(document.getElementsByClassName('topArea')[0])!="undefined" ){
      document.getElementsByClassName('topArea')[0].style.overflowX = 'auto';
    }
    if(typeof(document.getElementsByClassName('userInfoScrollArea')[0])!="undefined" ){
      document.getElementsByClassName('userInfoScrollArea')[0].style.overflowX = 'auto';
    }
  }
}
export default
{
  ENV_FLAG,
  MENU_ID_TOP,
  MENU_ID_CLINIC,
  MENU_ID_NEWS,
  MENU_ID_NEWS_OPINIONS,
  MENU_ID_NEWS_MYMEDICALNEWS,
  MENU_ID_ENQUETE,
  MENU_ID_FEATURE,
  MENU_ID_FEATURE_BREAKTIME,
  MENU_ID_FEATURE_DOCTOR_LIBRARY,
  MENU_ID_NAVI,
  MENU_ID_LIFE,
  MENU_ID_LIFESTYLE_INDEX,
  MENU_ID_LIFESTYLE_PRIVILEGE,
  MENU_ID_LIFESTYLE_FEATURE,
  MENU_ID_LIFESTYLE_QA,
  RANKING_TYPE_YESTERDAY,
  RANKING_TYPE_WEEK,
  RANKING_TYPE_COMMENT,
  LOGIN_PAGE_URL,
  MEDY_ARTICLE_DETAIL_URL,
  currentMenuId,
  subPageCount,
  isPageBack,
  toggleFix
}

</script>