    },
    setListScrollTop: function () {
      // pulltoの場合、scrolltopを銘記
      this.$emit('setListScrollTop');
    },
    gotoArticleList: function () {
      // 一覧を遷移する
      this.setListScrollTop();
      if(this.menuId == this.Global.MENU_ID_CLINIC){
        this.$router.push({ name: 'clinicalList'});
      } else if(this.menuId == this.Global.MENU_ID_NEWS_OPINIONS) {
        this.$router.push({ name: 'medicalOpinions'});
      } else if(this.menuId == this.Global.MENU_ID_NEWS_MYMEDICALNEWS){
        this.$router.push({ name: 'medicalNews'});
      } else if(this.menuId == this.Global.MENU_ID_FEATURE_BREAKTIME){
        this.$router.push({ name: 'featureBreakTime'});
      } else if(this.menuId == this.Global.MENU_ID_FEATURE_DOCTOR_LIBRARY){
        this.$router.push({ name: 'featureDoctorLibrary'});
      } else if(this.menuId == this.Global.MENU_ID_LIFESTYLE_INDEX){
        this.$router.push({ name: 'medyLifeStyle'});
      } else if(this.menuId == this.Global.MENU_ID_LIFESTYLE_QA){
        this.$router.push({ name: 'lifeStyleQA'});
      } else if (this.menuId == this.Global.MENU_ID_ENQUETE) {
        this.$router.push({ name: 'enqueteList'});
      }
    },
    getArticleListByGenre: function(genreId, loaded = null){
      this.isLoadingFail = false;
      this.isListInitLoadingFinished = false;
      this.loadingText = 'loading...';
      this.isNoData = false;
      this.genreId = genreId;

      //TODO DELETE
      if(genreId == 4){
        this.fakeLoadingOk = false;
      } else {
        this.fakeLoadingOk = true;
      }

      if(loaded == null){
        this.articles = [];
      }

      this.getArticleList(loaded);
    },
    doIf101109Success: function(response){
      var _this = this;
      if (response.data != null && response.data.result == "0000") {
        _this.isLoadingFail = false;
        if (!_this.isTop) {
          _this.loadingText = 'loading...';
        }
        var data = response.data;

        // 記事データがない場合
        if ((data.articles == undefined) || (data.articles.length == 0)) {
          _this.isNoData = true;
        } else {
          // リーフラッシュする場合、記事リストを初期化する
          if (_this.loaded != null) {
            _this.articles = [];
          }
          for (var i=0; i < data.articles.length; i++) {
            var articleInfo = {
              articleId: data.articles[i].articleId,
              articleImg: data.articles[i].externalLinkImg,
              articleTitle: data.articles[i].articleTitle,
              commentCount: data.articles[i].commentInfo.commentCount,
              commentDisplayFlag: data.articles[i].commentInfo.commentDisplayFlag,
              articleTypeName: null,
              articleStartDate: data.articles[i].nMinute,
              articleType: data.articles[i].articleType
            };
            if (typeof (data.articles[i].externalLinkImg) != "undefined" && data.articles[i].externalLinkImg.slice(0,5) == "http:") {
              articleInfo.articleImg = "https" + data.articles[i].externalLinkImg.substr(4);
            }
            if (typeof (data.articles[i].articleTypeName) != "undefined") {
              articleInfo.articleTypeName = data.articles[i].articleTypeName;
            }
            _this.articles.push(articleInfo);
          }

          if (typeof(data.login_campaign) != "undefined" && data.login_campaign != null){
            // TODO onLoginCampaignDialog
          }

          // 記事一覧もっと見るを実施する場合、オフセットを設定する
          if (!_this.isTop && _this.loaded == null) {
            // オフセットを保持する
            if (typeof (data.offset) != "undefined" && data.offset != null && data.offset != '') {
              _this.articleOffset = parseInt(data.offset);
            }
            if (typeof (data.ad_offset) != "undefined" && data.ad_offset != null && data.ad_offset != '') {
              _this.adOffset = parseInt(data.ad_offset);
            }
            if (typeof (data.ad_over) != "undefined" && data.ad_over != null && data.ad_over != '') {
              _this.adOver = data.ad_over;
            }
          }

          // TODO GA送信を実施

          if(_this.loaded !== null){
            loaded('done');
          }
          if (_this.isTop) {
            _this.isTopInitLoadingFinished = true;
          } else {
            _this.isListInitLoadingFinished = true;
          }
        }
      } else {
        // APIレスポンスからエラーが発生する場合
        _this.isLoadingFail = true;
        _this.loadingText = '再読み込み';
        if (_this.loaded != null) {
          loaded("fail");
        }
      }
    },
    doIf101109Fail: function(){
      var _this = this;
      _this.isLoadingFail = true;
        _this.loadingText = '再読み込み';
        if (_this.loaded != null) {
          loaded("fail");
        }
    },
    doIf101109Finally: function(){
      var _this = this;
      _this.$emit("changeLoadingFlag",false);
    },
    getArticleList: function (loaded=null) {
    
      // TODO FOR DEVELOP
      if (this.Global.ENV_FLAG == "develop") {
        this.getFakeArticleList(loaded);
        return;
      }
    
      var _this = this;
      _this.loaded = loaded;
      if(!_this.isListCanExist){
        return;
      }
      if (_this.isLoadingFail) {
        _this.$emit("changeLoadingFlag", false);
        return;
      }


      this.param101109.param.bgflg = 1;
      this.param101109.param.menu_id = _this.menuId;

      if(_this.isTop){
        // TOPの場合、パラメータを設定する
        _this.articleOffset = null;
        _this.adOffset = null;
        _this.adOver = null;
        _this.param101109.param.displayFlag = 0;
      } else {
        // 記事一覧の場合、パラメータを設定する
        if (loaded != null) {
            // リーフラッシュする場合、OFFSETを初期化する
            _this.articleOffset = null;
            _this.adOffset = null;
            _this.adOver = null;
          } else {
            // もっと見る場合
            if (_this.articleOffset != null) {
              _this.param101109.param.offset = _this.articleOffset;
            }
            if (_this.adOffset != null) {
              _this.param101109.param.ad_offset = _this.ad_offset;
            }
            if (_this.adOver != null) {
              _this.param101109.param.ad_over = _this.adOver;
            }
            _this.param101109.param.displayFlag = 1;
          }
          _this.param101109.param.displayFlag = 1;
      }

      _this.APILoader.apiDoGet(_this.param101109.URL,_this.param101109.getParamStr(),
          _this.doIf101109Success,_this.doIf101109Fail,_this.doIf101109Finally);

    },
    reLoadArticleList: function () {
      // load fail の場合、リロード
      if(this.isLoadingFail && this.isTop){
        this.isLoadingFail = false;
        this.getArticleList();
      } else if(this.isLoadingFail && typeof(this.$parent.$parent.isArticleLoading) != "undefined" && !this.$parent.$parent.isArticleLoading){
        this.$emit("changeLoadingFlag", true);
        this.isLoadingFail = false;
        this.loadingText = 'loading...';
        this.getArticleList();
      }
    },
    getFakeArticleList: function(loaded = null) {
    
      var _this = this;
      if(!_this.isListCanExist){
        return;
      }
      if (_this.isLoadingFail) {
        _this.$emit("changeLoadingFlag", false);
        return;
      }

      //TODO AJAX
      setTimeout(() => {
        if(_this.isTop){
          //TOP の 場合
          if(true){
            //data.result = '0000'
            // successのばあい
            _this.isLoadingFail = false;
            if(_this.articles.length == 0 && false){
              //data.articles.length == 0
              _this.isNoData = true;
            } else {
              _this.articles = _this.fakeArticles;
            }
            if(loaded !== null){
              loaded('done');
            }
            _this.isTopInitLoadingFinished = true;
          } else {
            // fail の場合
            _this.isLoadingFail = true;
            _this.loadingText = '再読み込み';
            if(loaded != null){
              loaded("fail");
            }
          }
        } else {
          // 記事一覧の場合
          if(_this.fakeLoadingOk){
            _this.isLoadingFail = false;
            _this.loadingText = 'loading...';
            if(_this.articles == 0 && (_this.genreId !== 0 && _this.genreId !== 1 && _this.genreId !== 2)){
              //data.articles.length == 0
              _this.isNoData = true;
            } else {
              if(loaded != null){
                _this.articles = _this.fakeArticles;
              } else {
                 _this.articles = _this.articles.concat(_this.fakeArticles);
              //TODO AJAX CONTROL
                if(_this.isListInitLoadingFinished){
                  _this.articles = _this.articles.concat(_this.fakeArticles);
                  _this.articles = _this.articles.concat(_this.fakeArticles);
                }
              }

              //TODO DELETE
              if(_this.genreId == 2){
                _this.fakeLoadingOk = false;
              }
            }
            if(loaded !== null){
              loaded('done');
            }
            _this.isListInitLoadingFinished = true;
          } else {
            // fail の場合
            _this.isLoadingFail = true;
            _this.loadingText = '再読み込み';
            if(loaded != null){
              loaded("fail");
            }
          }
        }
        _this.$emit("changeLoadingFlag",false);
      }, 2000);
    }
  }
}
</script>

