<template>
  <div :class="['pickupArticle', isLifeStyleQA ? 'qaArticle' : '', isLast ? 'lastArticle' : '']" @click="gotoArticleDetail(article.articleId)">
    <div v-if="!isLifeStyleQA">
      <div class="image" :style="makeBackImgUrl"></div>
      <div class="titleIndex">
        <span>{{sortNo}}</span>
      </div>
      <div class="title">
        <span class="titletest">{{article.articleTitle}}</span>
      </div>
    </div>
    <div v-else>
      <div class="qaMark">Q.</div>
      <div class="qaArticleTitle">{{article.articleTitle}}</div>
      <div class="qaRankOpinionsArea">
        <div class="qaOpinionArea">
          {{article.commentCount}}&nbsp;Opinions
        </div>
      </div>
      <div class="qaArticleTypeName">{{article.articleTypeName}}</div>
    </div>
  </div>
</template>

<script>
export default {
  name: "pickUpArticle",
  props: {
    article: Object,
    sortNo: Number,
    isLast: {
      type: Boolean,
      default: false
    },
    isLifeStyleQA: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
    };
  },
  created: function () {
  },
  mounted: function () {
  },
  activated: function () {
  },
  computed: {
    makeBackImgUrl: function () {
      return 'background-image:url(' + this.article.articleImg + ')';
    }
  },
  methods: {
    gotoArticleDetail: function (id) {
      this.$emit('setListScrollTop');
      this.$router.push({ name: 'articleDetail', params: { articleId: id } });
    }
  }
};
</script>

<style scope>
.pickupArticle {
  width: 100%;
  height: 70px;
  box-shadow: inset 0 -1px 0 0 var(--white-three);
  position: relative;
}

.pickupArticle.qaArticle{
  width: calc(100% - 28px);
  height: auto;
  min-height: 43px;
  padding: 12px 0 12px 0;
  margin-left: 12px;
}

.pickupArticle.lastArticle{
  box-shadow: none;
}

.pickupArticle .image {
  width: 74px;
  height: 46px;
  margin-top: 12px;
  margin-left: 12px;
  background-color: var(--black-10);
  background-repeat: no-repeat;
  background-attachment: inherit;
  background-size: cover;
  display: inline-block;
}

.pickupArticle .titleIndex {
  position: absolute;
  top: 28px;
  margin-left: 94px;
  width: 8px;
  height: 15px;
  font-family: Roboto;
  font-size: 13px;
  font-weight: 500;
  line-height: 15px;
  color: var(--tealish);
}

.pickupArticle .title {
  position: absolute;
  top: 15px;
  height: 40px;
  margin-left: 107px;
  width: calc((100% - 119px));
  height: 40px;
  display: flex;
  align-items: left;
  justify-content: center;
  flex-direction: column;
}

.pickupArticle .title span {
  max-height: 40px;
  word-wrap: break-word;
  word-break: break-all;
  text-overflow: ellipsis;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
  overflow: hidden;
  font-size: 14px;
  font-weight: 300;
  vertical-align: middle;
  display: -webkit-box;
  color: var(--black);
}

.pickupArticle.qaArticle .qaMark{
  height: 16px;
  font-family: Roboto;
  font-size: 14px;
  font-weight: 500;
  color: var(--tealish);
  display: inline-block;
  vertical-align: top;
  line-height: 14px;
  margin-top: 4px;
  margin-right: 5px;
  position: absolute;
}

.pickupArticle.qaArticle .qaArticleTitle{
  max-height: 40px;
  font-size: 14px;
  font-weight: 300;
  line-height: 20px;
  color: var(--black);
  width: calc(100% - 24px);
  display: inline-block;
  margin-bottom: 4px;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
  display: -webkit-box;
  overflow: hidden;
  word-break: break-all;
  margin-left: 19px;
}

.pickupArticle.qaArticle .qaRankOpinionsArea{
  position: relative;
  height: 18px;
  display: inline-block;
  width: 100%;
  vertical-align: top;
}

.pickupArticle.qaArticle .qaRankOpinionsArea .qaOpinionArea{
  height: 18px;
  font-size: 12px;
  font-weight: 300;
  line-height: 18px;
  color: var(--tealish);
  display: inline-block;
  margin-left: 19px;
  vertical-align: top;
}

.pickupArticle.qaArticle .qaArticleTypeName{
  height: 18px;
  font-size: 12px;
  font-weight: 300;
  color: var(--brown-grey);
  display: inline-block;
  line-height: 18px;
  position: absolute;
  right: 12px;
  bottom: 12px;
}
</style>