<script>
import OpinionList from "@/components/cmptsClinical/OpinionList.vue"
import ThankList from "@/components/cmptsClinical/ThankList.vue"
import FollowList from "@/components/cmptsClinical/FollowList.vue"
import FocousList from "@/components/cmptsClinical/FocousList.vue"
import PullTo from "@/components/vue-pull-to/vue-pull-to.vue"

  export default {
    name: 'interestType',
    components:{
      OpinionList,
      ThankList,
      FollowList,
      FocousList,
      PullTo,
    },
    mounted: function () {
      this.init()
    },
    data() {
      return {
        myImage: require('@/assets/medyImages/avatar.jpg'),
        username: '9文字制限なのかな',
        effectInfo: '美容・アンチエイジング',
        location: '泉ガーデンクリニック',
        contents: 'プロフィールプロフィールプロフィールプロフィールプロフィールプロフィールプロフィールプロフィールプロフィールプロフィールプロフィールプロフィールプロフィールプロフィールプロフィールプロフィールプロフィールプロフィールプロフィールプロフィール',
        number: 112,
        up: false,
        explain: false,
        follow: 21,
        focus: 8,
        opiClick: false,
        thaClick: false,
        folClick: false,
        focClick: false,
        refLoading: true,
        loadings: false,
      }
    },
    methods: {
      init() {
        this.$refs.pageScroll.style.height = window.innerHeight - 42 + 'px'
      },
      goBack() {
        if (window.history.length > 1) {
          this.$router.go(-1)
        } else {
          this.$router.push('/')
        }
      },
      goto() {
        this.$router.push({ name: 'proSet' });
      },
      handleScroll() {
        var _this = this
        var allHeight = this.$refs.scrollList.scrollHeight
        var needRefreshHeight = allHeight - window.innerHeight
        var scrollTop = document.getElementsByClassName("scroll-container")[0].scrollTop
        if (scrollTop >= needRefreshHeight && !this.refLoading) {
          setTimeout(function() {
            if (!_this.opiClick) {
              _this.$refs.opinionList.addOutput()
            } else if (_this.thaClick) {
              _this.$refs.thankList.addOutput()
            } else if (_this.folClick) {
              _this.$refs.followList.addOutput()
            } else if (_this.focClick) {
              _this.$refs.focousList.addOutput()
            }
          }, 800)
        }
      },
      refChange() {
        this.loadings = true
        this.refLoading = false
        this.handleScroll()
      },
      showExplain() {
        this.up = !this.up
        this.explain = !this.explain        
      },
      opinionClick() {
        this.thaClick = false
        this.folClick = false
        this.focClick = false
        this.opiClick = false
      },
      thanksClick() {
        this.thaClick = true
        this.opiClick = true
        this.folClick = false
        this.focClick = false
      },
      followClick() {
        this.thaClick = false
        this.opiClick = true
        this.folClick = true
        this.focClick = false  
      },
      foucusClick() {
        this.thaClick = false
        this.opiClick = true
        this.folClick = false
        this.focClick = true
      }
    },
  }
</script>

