<template>
  <div class="feature-doctorLibrary">
    <div class="doctorLibraryHeaderCol">
      <div class="doctorLibraryHeader" :style="'background-image: linear-gradient(to bottom, rgba(51, 51, 51, 0.5), rgba(51, 51, 51, 0.5)),url(' + backGroundImg + ');'">
        <div class="back" @click="goBack()">
          <img src="@/assets/medyImages/icon-navbar-back.png" srcset="@/assets/medyImages/icon-navbar-back@2x.png 2x, @/assets/medyImages/icon-navbar-back@3x.png 3x"/>
        </div>

        <div class="doctorLibraryTitle">
          <span class="doctorLibraryText">Doctor's Library</span>
        </div>

        <div class="search" @click="goSearch()">
          <img src="@/assets/medyImages/search-white.png" srcset="@/assets/medyImages/search-white@2x.png 2x, @/assets/medyImages/search-white@3x.png 3x"/>
        </div>
      </div>
    </div>
    <div class="doctorLibraryPullCol">
      <pull-to ref="doctorLibraryListPull" @scroll="scrollForLoad" :is-bottom-bounce="false" :top-load-method="refresh">
        <div class="doctorLibraryTopArea">
          <div class="topImgCol" :style="'background-image: linear-gradient(to bottom, rgba(51, 51, 51, 0.5), rgba(51, 51, 51, 0.5)),url(' + backGroundImg + ');'"></div>
          <div class="doctorLibraryTop">
            <div class="doctorLibraryTitleArea">
              <!-- <div class="introduceAreaTitle">Doctor's Library</div> -->
              <div class="introduceAreaDetail">医師が厳選した「この一冊」を紹介するページです。たくさんのレビューの中から、お気に入りの一冊を探してみてはいかがでしょうか？</div>
            </div>
          </div>
        </div>
        <div class="articleListCol" ref="articleListCol">
          <ArticleList ref="articleListDoctorLibrarys" :menuId="Global.MENU_ID_FEATURE_DOCTOR_LIBRARY" :isTop="false" @setListScrollTop="setListScrollTop" @changeLoadingFlag="changeLoadingFlag"/>
        </div>
      </pull-to>
    </div>
    <GoTopMark @goTop="goTop" v-if="isTopMarkShow"/>
  </div>
</template>

<script>
import ArticleList from "@/components/cmptsClinical/ArticleList.vue"
import PullTo from "@/components/vue-pull-to/vue-pull-to.vue"
import GoTopMark from "@/components/cmptsCommon/GoTopMark.vue"
import BackGroundImg from "@/assets/medyImages/feature_doctor_title.jpg"

export default {
  name: 'featureDoctorLibraryList',
  components: {
    ArticleList,
    GoTopMark,
    PullTo
  },
  data: function () {
    return {
      listScrollTop: 0,
      isArticleLoading: false,
      backGroundImg: BackGroundImg,
      isTopMarkShow: false,
      tabId: 1,
    }
  },
  mounted: function () {
    this.init();
  },
  activated: function () {
    if (this.$store.state.isPageBack) {
      // gobackの場合、keep-alliveでcacheを利用する、画面をrefreshしない
      if (this.listScrollTop > 0) {
        this.$refs.doctorLibraryListPull.setBackScrollPos(this.listScrollTop);
        this.listScrollTop = 0;
      }
    } else {
      // 普通遷移の場合、画面初期化処理を行う
      this.init();
    }
  },
  methods: {
    init: function () {
      // pulltoの場合、メインブロックの高さ
      this.$refs.articleListCol.style.height = window.innerHeight - 200 + 'px';
      this.isArticleLoading = false;
      this.isTopMarkShow = false;
      this.$refs.doctorLibraryListPull.scrollEl.addEventListener('scroll',this.isTopMarkCanShow);
    },
    goBack: function () {
      if (window.history.length > 1) {
        this.$router.go(-1)
      } else {
        this.$router.push('/')
      }
    },
    refresh: function (loaded) {
      //pull down refresh
      if (this.isArticleLoading) {
        loaded("fail");
        return;
      }
      this.$refs.articleListDoctorLibrarys.getArticleListByGenre(0, loaded);
    },
    setListScrollTop: function () {
      // pulltoの場合、scrolltopを銘記
      this.listScrollTop = this.$refs.doctorLibraryListPull.getScrollPos();
    },
    goSearch: function () {
      // search pageに遷移する
      this.setListScrollTop();
      this.$router.push({ name: 'search', params: { tabId: 5 } });
    },
    scrollForLoad: function () {
      // pull up load article list
      if (this.isArticleLoading) {
        return;
      }
      this.isArticleLoading = true;
      let listHeight = this.$refs.articleListDoctorLibrarys.$refs.listBlock.clientHeight;
      let outerHeight = this.$refs.articleListCol.clientHeight;
      let loadingPos = listHeight - outerHeight;
      if (listHeight - outerHeight > 0 && this.$refs.doctorLibraryListPull.getScrollPos() >= loadingPos - 48) {
        this.$refs.articleListDoctorLibrarys.getArticleList();
      } else {
        this.isArticleLoading = false;
      }
    },
    changeLoadingFlag: function (value) {
      this.isArticleLoading = value;
    },
    // goTopMark function
    isTopMarkCanShow: function(){
      this.isTopMarkShow = this.$refs.doctorLibraryListPull.getScrollPos() >= 250;
    },
    goTop: function(){
      this.$refs.doctorLibraryListPull.setBackScrollPos(0);
    }
  }
}
</script>

<style scope>
.feature-doctorLibrary .doctorLibraryHeaderCol{
  height: 42px;
  position: fixed;
  top: 0px;
  width: 100%;
  display: inline-block;
  overflow: hidden;
  vertical-align: top;
}

.feature-doctorLibrary .doctorLibraryHeader {
  height: 200px;
  width: 100%;
  display: inline-block;
  overflow: hidden;
  text-align: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.feature-doctorLibrary .doctorLibraryHeader .back {
  width: 24px;
  height: 24px;
  display: inline-block;
  position: absolute;
  top: 8px;
  left: 8px;
  line-height: 26px;
  overflow: hidden;
}

.feature-doctorLibrary .doctorLibraryHeader .back img {
  width: 7px;
}

.feature-doctorLibrary .doctorLibraryHeader .doctorLibraryTitle {
  display: inline-block;
  position: relative;
  top: 8px;
  height: 24px;
}

.feature-doctorLibrary .doctorLibraryHeader .doctorLibraryTitle .doctorLibraryText {
  font-size: 16px;
  line-height: 21px;
  height: 24px;
  color: var(--white-two);
}

.feature-doctorLibrary .doctorLibraryHeader .search {
  display: inline-block;
  position: absolute;
  right: 12px;
  top: 9px;
  width: 24px;
  height: 24px;
  overflow: hidden;
}

.feature-doctorLibrary .doctorLibraryHeader .search img {
  width: 24px;
  position: relative;
  margin-top: -2px;
}

.feature-doctorLibrary .doctorLibraryTopArea {
  position: relative;
  height: 103px;
  width: 100%;
  padding-top: 55px;
  overflow: hidden;
}

.feature-doctorLibrary .doctorLibraryTopArea .topImgCol {
  position: absolute;
  height: 200px;
  width: 100%;
  top: -42px;
  background-repeat: no-repeat;
  background-size: cover;
}

.feature-doctorLibrary .doctorLibraryTop {
  width: 100%;
  text-align: center;
}

.feature-doctorLibrary .doctorLibraryTop .doctorLibraryTitleArea {
  width: 100%;
  position: relative;
}

/* .feature-doctorLibrary .doctorLibraryTop .doctorLibraryTitleArea .introduceAreaTitle {
  width: 100%;
  height: 29px;
  font-size: 20px;
  font-weight: 500;
  line-height: 29px;
  color: var(--white-two);
  text-align: left;
  margin: 0 0 0 15px;
} */

.feature-doctorLibrary .doctorLibraryTop .doctorLibraryTitleArea .introduceAreaDetail {
  width: calc(100% - 31px);
  height: 36px;
  font-size: 12px;
  color: var(--white-two);
  text-align: left;
  margin: 31px auto 0 16px;
}

.feature-doctorLibrary .doctorLibraryPullCol {
  margin-top: 42px;
  overflow: hidden;
}

.feature-doctorLibrary .articleListCol {
  position: relative;
}
</style>
