<script>
import AcceptList from "@/components/cmptsClinical/AcceptList.vue"
import UseList from "@/components/cmptsClinical/UseList.vue"
import UseLessList from "@/components/cmptsClinical/UseLessList.vue"
import PullTo from "@/components/vue-pull-to/vue-pull-to.vue"
export default {
name: 'pointList',
    components:{
      AcceptList,
      UseList,
      UseLessList,
      PullTo,
    },
     mounted: function () {
      this.init()
    },
  data() {
    return {
        personFlag: true,
        up: false,
        down: true,
        explain: false,
        follow: 21,
        focus: 8,
        opiClick: false,
        thaClick: false,
        folClick: false,
        focClick: false,
        refLoading: true,
        loadings: false,
        popUpWord:'ご注意事項ご注意事項ご注意事項ご注意事項ご注意事項ご注意事項ご注意事項ご注意事項ご注意事項ご注意事項',
        popOff:false,
        writeUnable:true,
        acceptStyle:true,
        buttonAcceptStyle:true,
        acceptOn:true,
        useOn:false,
        uselessOn:false,
        buttonUseStyle:false,
        useStyle:false,
        loadImg:true,
        reload:true,
        loading:false,
    }
  },
  methods: {
      init() {
        this.$refs.pageScroll.style.height = window.innerHeight - 42 + 'px'
      },
      handleScroll() {
        var _this = this
        var allHeight = this.$refs.scrollList.scrollHeight
        var needRefreshHeight = allHeight - window.innerHeight
        var scrollTop = document.getElementsByClassName("scroll-container")[0].scrollTop
        if (scrollTop >= needRefreshHeight && !this.refLoading) {
          setTimeout(function() {
            if (!_this.opiClick) {
            //   _this.$refs.opinionList.addOutput()
            _this.$refs.acceptList.addOutput()
            } else if (_this.folClick) {
              _this.$refs.useList.addOutput()
            } else if (_this.focClick) {
              _this.$refs.useLessList.addOutput()
            }
          }, 800)
        }
      },
     refChange() {
        this.loadings = true
        this.refLoading = false
        this.handleScroll()
      },
      personClick() {
        this.personFlag = !this.personFlag
      },
      opinionClick() {
        this.thaClick = false
        this.folClick = false
        this.focClick = false
        this.opiClick = false
         this.acceptOn=true
        this.useOn=false
        this.uselessOn=false
        this.acceptStyle=true
        this.buttonAcceptStyle=true
      },
      followClick() {
        this.thaClick = false
        this.opiClick = true
        this.folClick = true
        this.focClick = false  
         this.useOn=true
        this.acceptOn=false
        this.uselessOn=false
        this.buttonUseStyle=true
        this.useStyle=true
        this.acceptStyle=false
      },
      foucusClick() {
        this.thaClick = false
        this.opiClick = true
        this.folClick = false
        this.focClick = true
        this.uselessOn=true
        this.acceptOn=false
        this.useOn=false
      },
    notePopUp:function(){
         this.popOff= !this.popOff
    },
    closePopUp:function(){
        this.popOff=false
    },
    clickAccept:function(){
        this.acceptOn=true
        this.useOn=false
        this.uselessOn=false
        this.acceptStyle=true
        this.buttonAcceptStyle=true
    },
    clickUse:function(){
        this.useOn=true
        this.acceptOn=false
        this.uselessOn=false
        this.buttonUseStyle=true
        this.useStyle=true
        this.acceptStyle=false
    },
    clickUseless:function(){
        this.uselessOn=true
        this.acceptOn=false
        this.useOn=false
    },
     goBack(){
      if (window.history.length > 1) {
        this.$router.go(-1);
      } else {
        this.$router.push("/");
      }
    },
    loadOn(){
        this.loading=true
        this.loadImg=false
        this.reload=false
    },
    clickUseButton(){
        this.$router.push('/pointExchange')
    }
    }
  }
</script>

