import Vue from 'vue'
import App from './App.vue'
import router from './router'
import './registerServiceWorker'
import axios from 'axios'
import Toasted from 'vue-toasted';
import global from './components/Global.vue'
import apiLoader from './components/APILoader.vue'
import store from './store'

Vue.config.productionTip = false

/* AXIOS共通定義 */
Vue.prototype.$axios = axios

/* TOAST共通定義 */
var toastedOptions = {
    position: 'bottom-center',
    duration: 1000,
}

Vue.use(Toasted, toastedOptions)

/* 共通変数定義 */
Vue.prototype.Global = global
Vue.prototype.APILoader = apiLoader
/* routerでパス切替管理 */
router.beforeEach((to, from, next) => {
    // uerAgentで画面遷移先を判断する
    var ua = navigator.userAgent;
    var isRequesrFromAndroid = (ua.indexOf('Android') > -1 && ua.indexOf('D2P Android') == -1);
    var isRequesrFromiOS = (ua.indexOf('Mac') > -1 && ua.indexOf('D2P iOS') == -1 && (ua.indexOf('iPhone') > -1 || ua.indexOf('iPod') > -1));
    if (isRequesrFromAndroid || isRequesrFromiOS) {
        // Android又はiOSでリクエスト場合、PWA画面に遷移する
        if (to.name == null && (to.matched == null || to.matched.length == 0)) {
            // PWA未定義URLの場合、PWAのTOP画面に遷移する
            next('/');
        } else {
            next();
        }
    } else {
        next(false);
        if (to.name == 'articleDetail' && typeof(to.params.articleId) != 'undefined' && to.params.articleId != null) {
            // 記事詳細画面の場合、PCの記事詳細画面に遷移する
            window.location.href = global.MEDY_ARTICLE_DETAIL_URL + to.params.articleId;
        } else {
            // 上記以外の場合、PCのTOP画面に遷移する
            window.location.href = global.LOGIN_PAGE_URL;
        }
    }
})

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
