<template>
  <header :class="['trans-header', pageHeaderHide ? 'is-hidden' : '']">
    <div class="header">
      <div class="topDiv">
        <!-- menu -->
        <img
          class="menu"
          src="../assets/medyImages/icon-navbar-menu.png"
          srcset="../assets/medyImages/icon-navbar-menu@2x.png 2x, ../assets/medyImages/icon-navbar-menu@3x.png 3x"
          @click="showMenu()"
        >
        <!-- logo -->
        <img class="logo" src="../assets/medyImages/logo.png" @click="goTop">
        <svg
          class="notice"
          @click="goToNotice"
          xmlns="http://www.w3.org/2000/svg"
          width="24"
          height="24"
          viewBox="0 0 24 24"
        >
          <g fill="none" fill-rule="evenodd">
            <path
              fill="#333"
              d="M18.666 14.827c.293.355.524.67.693.948.17.277.186.554.05.832a.827.827 0 0 1-.659.482 5.27 5.27 0 0 1-.71.067h-3.163c0 .787-.282 1.458-.846 2.012a2.811 2.811 0 0 1-2.046.832c-.8 0-1.483-.277-2.047-.832a2.719 2.719 0 0 1-.845-2.012H5.997c-.259 0-.527-.03-.803-.092a.843.843 0 0 1-.6-.457c-.136-.278-.124-.563.034-.857.157-.294.372-.618.642-.973.305-.388.637-.829.998-1.322.36-.494.541-1.007.541-1.54V8.44c0-.754.133-1.46.398-2.12a5.501 5.501 0 0 1 1.09-1.73 5.052 5.052 0 0 1 1.641-1.165A4.908 4.908 0 0 1 11.968 3a4.95 4.95 0 0 1 2.046.424c.632.283 1.182.671 1.65 1.165.467.493.834 1.07 1.099 1.73.265.66.397 1.366.397 2.12v3.477c0 .543.172 1.07.516 1.58.344.51.674.954.99 1.33zm-6.681 4.059c.485 0 .899-.17 1.243-.508.344-.338.516-.746.516-1.222h-3.518c0 .476.172.884.516 1.222.344.338.758.508 1.243.508zm6.173-2.862a5.578 5.578 0 0 0-.186-.24 16.37 16.37 0 0 1-1.362-1.83 3.66 3.66 0 0 1-.6-2.038V8.439a4.48 4.48 0 0 0-.313-1.68 4.361 4.361 0 0 0-.854-1.364 4.05 4.05 0 0 0-1.277-.923 3.754 3.754 0 0 0-1.598-.34c-.564 0-1.091.113-1.581.34a4.05 4.05 0 0 0-1.277.923 4.24 4.24 0 0 0-.846 1.364 4.589 4.589 0 0 0-.305 1.68v3.477c0 .72-.211 1.383-.634 1.988a52.941 52.941 0 0 1-1.336 1.838c-.073.1-.138.194-.194.282h12.363z"
            ></path>
          </g>
        </svg>
      </div>

      <div class="navDiv" ref="navDivCol">
        <div class="leftBar"></div>

        <div class="rightBar"></div>

        <div class="navWidCol" ref="navWidCol">
          <div
            ref="headerItems"
            v-for="(item, index) in menuInfos"
            :key="index"
            :class="['headerItem',(index == menuInfos.length -1) ? 'lastMenu' : '', (activeMenu == item.menuId) ? 'active' : '']"
            @click="switchMenu(item.menuId, index)"
          >{{item.menuName}}</div>
        </div>
        <div class="chooseMark" ref="chooseMark"></div>
      </div>
    </div>
  </header>
</template>

<script>
export default {
  name: "medyHeader",
  props: {
    pageHeaderHide: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      activeMenu: this.Global.currentMenuId,
      menuInfos: [
        { menuId: this.Global.MENU_ID_CLINIC, menuName: "臨床Q&A" },
        { menuId: this.Global.MENU_ID_NEWS, menuName: "医療ニュース" },
        { menuId: this.Global.MENU_ID_ENQUETE, menuName: "アンケート" },
        { menuId: this.Global.MENU_ID_FEATURE, menuName: "特集・企画" },
        { menuId: this.Global.MENU_ID_LIFE, menuName: "ライフスタイル" }
      ]
    };
  },
  components: {
  },
  mounted: function () {
    this.init();
  },
  activated: function () {
    if (this.$store.state.isPageBack) {
      // gobackの場合、keep-alliveでcacheを利用する、画面をrefreshしない
    } else {
      // 普通遷移の場合、画面初期化処理を行う
      this.init();
    }
  },
  methods: {
    init: function () {

      let _this = this;
      let navWid = 0;
      let menuIndex = 0;
      _this.menuInfos.forEach(function (menuItem, itemIndex) {
        if (menuItem.menuId == _this.activeMenu) {
          menuIndex = itemIndex;
          return false;
        }
      });

      _this.$refs.headerItems.forEach(function (item, index) {
        navWid += item.offsetWidth + 18;
      });

      _this.$refs.navWidCol.style.width = navWid + 5 + "px";
      _this.moveChooseMark(menuIndex);
      _this.$refs.chooseMark.style.transition = "all 250ms ease-in-out";
    },
    showMenu: function () {
      this.Global.toggleFix(true);
      this.$store.dispatch("showSideMenu");
    },
    switchMenu: function (menuId, index) {
      if (this.activeMenu == menuId) {
        return;
      }
      this.activeMenu = menuId;
      this.Global.currentMenuId = menuId;
      this.$emit("update:activeMenu", menuId);
      this.moveChooseMark(index);
    },
    moveChooseMark: function (index) {
      let moveLen = 0;
      this.$refs.headerItems.forEach(function (item, itemIndex) {
        if (itemIndex > index) {
          return false;
        } else if (itemIndex == index) {
          moveLen += item.offsetWidth / 2;
        } else {
          moveLen += item.offsetWidth;
        }
      })
      moveLen += 18 * index;
      moveLen -= 4;
      this.$refs.chooseMark.style.marginLeft = moveLen + 'px';

      let cenPos = window.innerWidth / 2;
      if (moveLen - cenPos <= 0) {
        this.$refs.navDivCol.scrollLeft = 0;
      } else if (moveLen - cenPos > parseInt(this.$refs.navWidCol.style.width) - window.innerWidth) {
        this.$refs.navDivCol.scrollLeft = parseInt(this.$refs.navWidCol.style.width) - window.innerWidth;
      } else {
        this.$refs.navDivCol.scrollLeft = moveLen - cenPos + 12;
      }
    },
    goToNotice: function () {
      // お知らせページに遷移
      this.$emit("setListScrollTop");
      this.$router.push({path:'/notice'});
    },
    goTop: function () {
      if(this.activeMenu == this.Global.MENU_ID_CLINIC){
        return;
      }
      this.switchMenu(this.Global.MENU_ID_CLINIC, 0);
    }
  }
};
</script>

<style scoped>
header {
  position: fixed;
  top: 0;
  left: 0px;
  bottom: 0px;
  width: 100%;
  height: 84px;
  background-color: var(--white-two);
  z-index: 8;
}

.header .topDiv {
  position: relative;
  width: 100%;
  height: 42px;
  background-color: var(--white-two);
}

.header .topDiv .menu {
  position: absolute;
  left: 12px;
  top: 9px;
  width: 24px;
  height: 24px;
  object-fit: contain;
}

.header .topDiv .logo {
  position: absolute;
  left: calc((100% - 81px) / 2);
  top: 9px;
  width: 81px;
  height: 24px;
  object-fit: contain;
}

.header .topDiv .notice {
  position: absolute;
  right: 12px;
  top: 9px;
  width: 24px;
  height: 24px;
  object-fit: contain;
}

.header .navDiv {
  width: 100%;
  height: 42px;
  box-shadow: inset 0 -1px 0 0 var(--white-three);
  background-color: var(--white-two);
  position: fixed;
  overflow-x: auto;
  overflow-y: hidden;
  z-index: 1;
}

.header .navDiv .leftBar {
  position: fixed;
  top: 42px;
  left: 0px;
  width: 15px;
  height: 41px;
  background-image: linear-gradient(
    to left,
    rgba(255, 255, 255, 0),
    var(--white-two)
  );
  z-index: 10;
}

.header .navDiv .rightBar {
  position: fixed;
  top: 42px;
  right: 0px;
  width: 15px;
  height: 41px;
  background-image: linear-gradient(
    to right,
    rgba(255, 255, 255, 0),
    var(--white-two)
  );
  z-index: 15;
}
.header .navDiv .navWidCol {
  position: absolute;
  overflow-y: hidden;
  width: 700px;
}

.header .navDiv .chooseMark {
  position: absolute;
  margin-left: 30px;
  margin-top: 31px;
  margin-bottom: 8px;
  width: 21px;
  height: 3px;
  border-radius: 1.5px;
  background-color: var(--tealish);
}

.header .navDiv .headerItem {
  position: relative;
  margin-right: 18px;
  margin-top: 6px;
  height: 20px;
  font-size: 14px;
  font-weight: 300;
  color: var(--black);
  padding-left: 12px;
  display: inline-block;
}

.header .navDiv .active {
  color: var(--tealish);
}

.header .navDiv .lastMenu {
  margin-right: 12px;
}

.trans-header.is-hidden {
  -webkit-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
}
.trans-header {
  background-color: var(--white-two);
  -webkit-transform: translateZ(0);
  transform: translateZ(0);
  will-change: transform;
  -webkit-transition: -webkit-transform 0.5s;
  transition: -webkit-transform 0.5s;
  transition: transform 0.5s;
  transition: transform 0.5s, -webkit-transform 0.5s;
}
.trans-header::after {
  clear: both;
  content: "";
  display: block;
}
</style>