<template>
  <div class="medyLifeStyle">
    <div class="medyLifeStyleHeaderCol">
      <div class="medyLifeStyleHeader" :style="'background-image: linear-gradient(to bottom, rgba(51, 51, 51, 0.5), rgba(51, 51, 51, 0.5)),url(' + backGroundImg + ');'">
        <div class="back" @click="goBack()">
          <img src="@/assets/medyImages/icon-navbar-back.png" srcset="@/assets/medyImages/icon-navbar-back@2x.png 2x, @/assets/medyImages/icon-navbar-back@3x.png 3x"/>
        </div>
        <div class="medyLifeStyleTitle">
          <span class="medyLifeStyleText">MEDY LIFESTYLE</span>
        </div>
      </div>
    </div>
    <div class="medyLifeStylePullCol">
      <pull-to ref="medyLifeStyleListPull" @scroll="scrollForLoad" :is-bottom-bounce="false" :top-load-method="refresh">
        <div class="medyLifeStyleTopArea">
          <div class="topImgCol" :style="'background-image: linear-gradient(to bottom, rgba(51, 51, 51, 0.5), rgba(51, 51, 51, 0.5)),url(' + backGroundImg + ');'"></div>
          <div class="medyLifeStyleTop">
            <div class="medyLifeStyleTitleArea">
              <div class="introduceAreaDetail">MEDYだけの情報やお得な特典・優待等のコンテンツを配信</div>
            </div>
          </div>
        </div>
        <div class="articleListCol" ref="articleListCol">
          <ArticleList ref="medyLifeStyleArticleList" :menuId="Global.MENU_ID_LIFESTYLE_INDEX" :isTop="false" @setListScrollTop="setListScrollTop" @changeLoadingFlag="changeLoadingFlag"/>
        </div>
      </pull-to>
    </div>
    <GoTopMark @goTop="goTop" v-if="isTopMarkShow"/>
  </div>
</template>

<script>
import ArticleList from "@/components/cmptsClinical/ArticleList.vue"
import PullTo from "@/components/vue-pull-to/vue-pull-to.vue"
import GoTopMark from "@/components/cmptsCommon/GoTopMark.vue"
import BackGroundImg from "@/assets/medyImages/topMedyLifeStyle_background.png"

export default {
  name: 'medyLifeStyleList',
  components: {
    ArticleList,
    GoTopMark,
    PullTo
  },
  data: function () {
    return {
      listScrollTop: 0,
      isArticleLoading: false,
      backGroundImg: BackGroundImg,
      isTopMarkShow: false,
      tabId: 1,
    }
  },
  mounted: function () {
    this.init();
  },
  activated: function () {
    if (this.$store.state.isPageBack) {
      // gobackの場合、keep-alliveでcacheを利用する、画面をrefreshしない
      if (this.listScrollTop > 0) {
        this.$refs.medyLifeStyleListPull.setBackScrollPos(this.listScrollTop);
        this.listScrollTop = 0;
      }
    } else {
      // 普通遷移の場合、画面初期化処理を行う
      this.init();
    }
  },
  methods: {
    init: function () {
      // pulltoの場合、メインブロックの高さ
      this.$refs.articleListCol.style.height = window.innerHeight - 200 + 'px';
      this.isArticleLoading = false;
      this.isTopMarkShow = false;
      this.$refs.medyLifeStyleListPull.scrollEl.addEventListener('scroll',this.isTopMarkCanShow);
      this.Global.currentMenuId = this.Global.MENU_ID_LIFE;
    },
    goBack: function () {
      if (window.history.length > 1) {
        this.$router.go(-1)
      } else {
        this.$router.push('/')
      }
    },
    refresh: function (loaded) {
      //pull down refresh
      if (this.isArticleLoading) {
        loaded("fail");
        return;
      }
      this.$refs.medyLifeStyleArticleList.getArticleListByGenre(0, loaded);
    },
    setListScrollTop: function () {
      // pulltoの場合、scrolltopを銘記
      this.listScrollTop = this.$refs.medyLifeStyleListPull.getScrollPos();
    },
    scrollForLoad: function () {
      // pull up load article list
      if (this.isArticleLoading) {
        return;
      }
      this.isArticleLoading = true;
      let listHeight = this.$refs.medyLifeStyleArticleList.$refs.listBlock.clientHeight;
      let outerHeight = this.$refs.articleListCol.clientHeight;
      let loadingPos = listHeight - outerHeight;
      if (listHeight - outerHeight > 0 && this.$refs.medyLifeStyleListPull.getScrollPos() >= loadingPos - 48) {
        this.$refs.medyLifeStyleArticleList.getArticleList();
      } else {
        this.isArticleLoading = false;
      }
    },
    changeLoadingFlag: function (value) {
      this.isArticleLoading = value;
    },
    // goTopMark function
    isTopMarkCanShow: function(){
      this.isTopMarkShow = this.$refs.medyLifeStyleListPull.getScrollPos() >= 250;
    },
    goTop: function(){
      this.$refs.medyLifeStyleListPull.setBackScrollPos(0);
    }
  }
}
</script>

<style scoped>
.medyLifeStyle .medyLifeStyleHeaderCol{
  height: 42px;
  position: fixed;
  top: 0px;
  width: 100%;
  display: inline-block;
  overflow: hidden;
  vertical-align: top;
}

.medyLifeStyle .medyLifeStyleHeader {
  height: 200px;
  width: 100%;
  display: inline-block;
  overflow: hidden;
  text-align: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.medyLifeStyle .medyLifeStyleHeader .back {
  width: 24px;
  height: 24px;
  display: inline-block;
  position: absolute;
  top: 8px;
  left: 8px;
  line-height: 26px;
  overflow: hidden;
}

.medyLifeStyle .medyLifeStyleHeader .back img {
  width: 7px;
}

.medyLifeStyle .medyLifeStyleHeader .medyLifeStyleTitle {
  display: inline-block;
  position: relative;
  top: 8px;
  height: 24px;
}

.medyLifeStyle .medyLifeStyleHeader .medyLifeStyleTitle .medyLifeStyleText {
  font-size: 16px;
  line-height: 21px;
  height: 24px;
  color: var(--white-two);
}

.medyLifeStyle .medyLifeStyleTopArea {
  position: relative;
  height: 78px;
  width: 100%;
  padding-top: 80px;
  overflow: hidden;
}

.medyLifeStyle .medyLifeStyleTopArea .topImgCol {
  position: absolute;
  height: 200px;
  width: 100%;
  top: -42px;
  background-repeat: no-repeat;
  background-size: cover;
}

.medyLifeStyle .medyLifeStyleTop {
  width: 100%;
  text-align: center;
}

.medyLifeStyle .medyLifeStyleTop .medyLifeStyleTitleArea {
  width: 100%;
  position: relative;
}

.medyLifeStyle .medyLifeStyleTop .medyLifeStyleTitleArea .introduceAreaDetail {
  width: calc(100% - 24px);
  max-height: 36px;
  font-size: 12px;
  color: var(--white-two);
  text-align: left;
  margin: auto auto 0 12px;
  line-height: 18px;
  position: relative;
}

.medyLifeStyle .medyLifeStylePullCol {
  margin-top: 42px;
  overflow: hidden;
}

.medyLifeStyle .articleListCol {
  position: relative;
}
</style>
