    mounted: function() {
      this.$refs.enqueteListContainer.style.height = window.innerHeight - 42 + 'px';
      this.$refs.enqueteListCol.style.height = window.innerHeight - 42 + 'px';
    },
    activated: function() {
      if (this.$store.state.isPageBack) {
        // gobackの場合、keep-alliveでcacheを利用する、画面をrefreshしない
        if (this.listScrollTop > 0) {
          this.$refs.enqueteListPull.setBackScrollPos(this.listScrollTop);
          this.listScrollTop = 0;
        }
      } else {
        // 普通遷移の場合、画面初期化処理を行う
        this.init();
      }
    },
    methods: {
      init: function() {
        this.isEnqueteLoading = false;
        this.isTopMarkShow = false;
        this.$refs.enqueteListPull.scrollEl.addEventListener('scroll',this.isTopMarkCanShow);
        this.getEnqueteList();
      },
      goBack: function() {
        if (window.history.length > 1) {
          this.$router.go(-1)
        } else {
          this.$router.push('/')
        }
      },
      scrollForLoad: function() {
        if (this.isEnqueteLoading) {
          return;
        }
        this.isEnqueteLoading = true;
        let listHeight = this.$refs.listBlock.clientHeight;
        let outerHeight = this.$refs.enqueteListCol.clientHeight;
        let loadingPos = listHeight - outerHeight;
        if (listHeight - outerHeight > 0 && this.$refs.enqueteListPull.getScrollPos() >= loadingPos - 48) {
          this.getMoreEnqueteList();
        } else {
          this.isEnqueteLoading = false;
        }
      },
      gotoEnqueteDetail: function (id) {
        this.listScrollTop = this.$refs.enqueteListPull.getScrollPos();
        this.$router.push({ name: 'enqueteDetail', params: { enqueteId: id }})
      },
      refresh: function (loaded) {
        //pull down refresh
        if (this.isEnqueteLoading) {
          loaded("fail");
          return;
        }
        this.getEnqueteList(loaded);
      },
      reloadEnqueteList: function() {
        this.getEnqueteList();
      }, 
      getEnqueteList: function(loaded = null) {
        if (this.isEnqueteLoading) {
          if (loaded != null) {
            loaded("fail");
          }
        }
        this.isEnqueteLoading = true;
        var _this = this;
        // TODO 完了したアンケートを検索する
        setTimeout(function () {
          if (loaded != null) {
            loaded('done');
          }
          _this.questionnaires = [];
          for (var i=0; i < _this.fakeEnqueteData.length; i++) {
            _this.questionnaires.push(_this.fakeEnqueteData[i]);
          }
          _this.isEnqueteLoading = false;
        }, 2000);
      },
      getMoreEnqueteList: function() {
        var _this = this;
        this.isEnqueteLoading = true;
        setTimeout(function () {
          for (var i=0; i < _this.fakeEnqueteData.length; i++) {
            _this.questionnaires.push(_this.fakeEnqueteData[i]);
          }
          _this.isEnqueteLoading = false;
        }, 2000);
      },
      isTopMarkCanShow: function() {
        this.isTopMarkShow = this.$refs.enqueteListPull.getScrollPos() >= 250;
      },
      goTop: function() {
        this.$refs.enqueteListPull.setBackScrollPos(0);
      }
