<template>
  <div class="featureTop">
    <div class="featureTopArea">
      <div class="topAreaBlock" @click="gotoBreaKTime()">
        <span>ちょっとひと息</span>
        <div class="showMoreImgArea">
          <img src="@/assets/medyImages/contents-more-small.png" srcset="@/assets/medyImages/contents-more-small@2x.png 2x, @/assets/medyImages/contents-more-small@3x.png 3x"/>
        </div>
      </div>
      <div class="topAreaBlock" @click="gotoDoctorLibrary()">
        <span>Doctor's Library</span>
        <div class="showMoreImgArea">
          <img src="@/assets/medyImages/contents-more-small.png" srcset="@/assets/medyImages/contents-more-small@2x.png 2x, @/assets/medyImages/contents-more-small@3x.png 3x"/>
        </div>
      </div>
    </div>
    <PickUpArticles ref="pickUpArticles" :menuId="menuId" @setListScrollTop="setListScrollTop"/>
    <div class="breathImgArea" :style="'background-image: linear-gradient(to bottom, rgba(191, 214, 232, 0.5), rgba(191, 214, 232, 0.5)),url(' + breathBarImg + ');'">
      <p class="breath-firstP">ちょっとひと息</p>
      <p class="breath-secondP">忙しい日々の合間に、ひと息つけるおもしろコンテンツを。</p>
    </div>
    <articleList ref="articleListOpinions" :menuId="Global.MENU_ID_FEATURE_BREAKTIME" @setListScrollTop="setListScrollTop"/>
    <div class="doctorImgArea" :style="'background-image: linear-gradient(to bottom, rgba(51, 51, 51, 0.5), rgba(51, 51, 51, 0.5)),url(' + doctorBarImg + ');'">
      <p class="doctor-firstP">Doctor's Library</p>
      <p class="doctor-secondP">医師が選んだ「この一冊」をご紹介。</p>
    </div>
    <articleList ref="articleListMyMedicalNews" :menuId="Global.MENU_ID_FEATURE_DOCTOR_LIBRARY" @setListScrollTop="setListScrollTop"/>
  </div>
</template>

<script>
import PickUpArticles from "@/components/cmptsClinical/PickUpArticles.vue"
import PickUpUsers from "@/components/cmptsClinical/PickUpUsers.vue"
import ArticleList from "@/components/cmptsClinical/ArticleList.vue"
import breathBarImage from "@/assets/medyImages/feature_breath_bar.jpg"
import doctorBarImage from "@/assets/medyImages/feature_doctor_bar.jpg"

export default {
  name: 'featureTop',
  prop: {
  },
  data: function () {
    return {
      // isNewsFeedSetting: false,
      // isGotFeedFinished: false,
      menuId: this.Global.MENU_ID_FEATURE,
      breathBarImg: breathBarImage,
      doctorBarImg: breathBarImage
    }
  },
  components: {
    PickUpArticles,
    PickUpUsers,
    ArticleList
  },
  mounted: function () {
    this.init();
  },
  activated: function () {
    if (this.$store.state.isPageBack) {
      // gobackの場合、keep-alliveでcacheを利用する、画面をrefreshしない
    } else {
      // 普通遷移の場合、画面初期化処理を行う
      this.init();
    }
  },
  methods: {
    init: function () {
      var _this = this;
    },
    setListScrollTop: function () {
      // pulltoの場合、scrolltopを銘記
      this.$emit('setListScrollTop');
    },
    gotoBreaKTime: function(){
       this.$router.push({ name: 'featureBreakTime'});
    },
    gotoDoctorLibrary: function(){
      this.$router.push({ name: 'featureDoctorLibrary'});
    }
  }
}
</script>

<style scope>
.featureTop .featureTopArea {
  height: 48px;
  width: 100%;
  position: relative;
  background-color: var(--white-four);
}

.featureTop .featureTopArea .topAreaBlock {
  width: 50%;
  height: 48px;
  display: inline-block;
  box-shadow: inset -1px 0 0 0 var(--white-three);
  position: relative;
  vertical-align: top;
}

.featureTop .featureTopArea .topAreaBlock span {
  height: 20px;
  font-size: 14px;
  font-weight: 300;
  color: var(--black);
  line-height: 48px;
  margin-left: 12px;
}

.featureTop .featureTopArea .topAreaBlock .showMoreImgArea {
  display: inline-block;
  position: absolute;
  width: 16px;
  height: 16px;
