}

.featureTop .doctorImgArea .doctor-firstP{
  height: 29px;
  font-size: 20px;
  font-weight: 300;
  color: var(--white-two);
  position: relative;
  margin: 13px auto 3px 12px;
}

.featureTop .doctorImgArea .doctor-secondP{
  height: 36px;
