<script type="text/javascript">

const URL = '/api/1/user/article/commentPC/get/articlelist'

var param = {
  appVersion:0,
  bgflg:null,
  query:null,
  offset:null,
  ad_offset:null,
  ad_over:null,
  menu_id:null,
  article_classification_id:null,
  commentSortFlag:null,
  displayFlag:null,
}

var getParamStr = function(){
  return "?appVersion=" + param.appVersion 
  + (param.bgflg != null ? ("&bgflg=" + param.bgflg):"" )
  + (param.query != null ? ("&query=" + param.query):"")
  + (param.offset != null ? ("&offset=" + param.offset):"")
  + (param.ad_offset != null ? ("&ad_offset=" + param.ad_offset):"")
  + (param.ad_over != null ? ("&ad_over=" + param.ad_over):"")
  + (param.menu_id != null ? ("&menu_id=" + param.menu_id):"")
  + (param.article_classification_id != null ? ("&article_classification_id=" + param.article_classification_id):"")
  + (param.commentSortFlag != null ? ("&commentSortFlag=" + param.commentSortFlag):"")
  + (param.displayFlag != null ? ("&displayFlag=" + param.displayFlag):"")
}

export default
{
  URL,
  param,
  getParamStr
}

</script>