<template>
  <div class="privilegeList">
    <div class="privilegeListHeaderCol">
      <div class="privilegeListHeader" :style="'background-image: linear-gradient(to bottom, rgba(51, 51, 51, 0.5), rgba(51, 51, 51, 0.5)),url(' + backGroundImg + ');'">
        <div class="back" @click="goBack()">
          <img src="@/assets/medyImages/icon-navbar-back.png" srcset="@/assets/medyImages/icon-navbar-back@2x.png 2x, @/assets/medyImages/icon-navbar-back@3x.png 3x"/>
        </div>

        <div class="privilegeListTitle">
          <span class="privilegeTitleText">特典・優待</span>
        </div>

      </div>
    </div>
    <div class="privilegeListPullCol">
      <pull-to ref="privilegeListPull" @scroll="scrollForLoad" :is-top-bounce="isCanPullDown" :is-bottom-bounce="false" :top-load-method="refresh">
        <div class="privilegeListTopArea">
          <div class="topImgCol" :style="'background-image: linear-gradient(to bottom, rgba(51, 51, 51, 0.5), rgba(51, 51, 51, 0.5)),url(' + backGroundImg + ');'"></div>
          <div class="privilegeListTop">
            <div class="privilegeListTitleArea">
              <div class="introduceAreaDetail">{{privilegeIntroText}}</div>
            </div>
          </div>
        </div>
        <div class="genreCol">
            <div class="genreArea" ref="genreArea" @scroll="listenScrollX" @touchend="reSetScroll">  
              <div class="genreScrollArea">
                <div :class="['genre', index == privilegeGenre.length - 1? 'last' : '', currentGenreId == genre.genreId ? 'active' : '' ]" :key="index" v-for="(genre, index) in privilegeGenre" @click="changeGenre(genre.genreId, $event)">
                  <span class="genreName">{{genre.genreName}}</span>
                </div>
              </div>
            </div>
          </div>
        <div class="articleListCol" ref="articleListCol">
          <ArticleList ref="privilegeListArticleList" :menuId="Global.MENU_ID_LIFESTYLE_PRIVILEGE" :isTop="false" @setListScrollTop="setListScrollTop" @changeLoadingFlag="changeLoadingFlag"/>
        </div>
      </pull-to>
    </div>
  <GoTopMark @goTop="goTop" v-if="isTopMarkShow"/>
  </div>
</template>

<script>
import ArticleList from "@/components/cmptsClinical/ArticleList.vue"
import PullTo from "@/components/vue-pull-to/vue-pull-to.vue"
import GoTopMark from "@/components/cmptsCommon/GoTopMark.vue"
import BackGroundImg from "@/assets/medyImages/privilege_background.png"

export default {
  name: 'privilegeList',
  components: {
    ArticleList,
    GoTopMark,
    PullTo
  },
  data: function () {
    return {
      currentGenreId: 0,
      privilegeGenre: [
        {
          genreId: 0,
          genreName: '全て'
        }
      ],
      fakeGenreData: [
        {
          genreId: 1,
          genreName: '循環器'
        },
        {
          genreId: 2,
          genreName: '血液内科'
        },
        {
          genreId: 3,
          genreName: '記事分類3'
        },
        {
          genreId: 4,
          genreName: '記事分類4'
        },
        {
          genreId: 5,
          genreName: '記事分類5'
        },
        {
          genreId: 6,
          genreName: '記事分類6'
        },
        {
          genreId: 7,
          genreName: '記事分類7'
        },
        {
          genreId: 8,
          genreName: '記事分類8'
        },
        {
          genreId: 9,
          genreName: '記事分類9'
        },
        {
          genreId: 10,
          genreName: '記事分類10'
        },
        {
          genreId: 11,
          genreName: '記事分類11'
        },
        {
          genreId: 12,
          genreName: '記事分類12'
        },
        {
          genreId: 13,
          genreName: '記事分類13'
        },
        {
          genreId: 14,
          genreName: '記事分類14'
        },
        {
          genreId: 15,
          genreName: '記事分類15'
        },
        {
          genreId: 16,
          genreName: '記事分類16'
        },
        {
          genreId: 17,
          genreName: '記事分類17'
        }
      ],
      //TODO ARTICLELIST COL
      privilegeIntroText: 'ldmedyさまへ特別なご案内がございます。',
      listScrollTop: 0,
      isArticleLoading: false,
      backGroundImg: BackGroundImg,
      isTopMarkShow: false,
      isCanPullDown: true,
      tabId: 7,
    }
  },
  mounted: function () {
    this.init();
  },
  activated: function () {
    if (this.$store.state.isPageBack) {
      // gobackの場合、keep-alliveでcacheを利用する、画面をrefreshしない
      if (this.listScrollTop > 0) {
        this.$refs.privilegeListPull.setBackScrollPos(this.listScrollTop);
        this.listScrollTop = 0;
      }
    } else {
      // 普通遷移の場合、画面初期化処理を行う
      this.init();
    }
  },
  methods: {
    init: function () {
      // pulltoの場合、メインブロックの高さ
      this.$refs.articleListCol.style.height = window.innerHeight - 242 + 'px';
      this.isArticleLoading = false;
      this.currentGenreId = 0;
      this.isTopMarkShow = false;
      this.$refs.privilegeListPull.scrollEl.addEventListener('scroll', this.isTopMarkCanShow);
      this.getGenreData();
      this.Global.currentMenuId = this.Global.MENU_ID_LIFESTYLE_QA;
    },
    getGenreData: function(){
      var _this = this;
      //TODO AJAX GET GENREDATA
      setTimeout(function () {
        let genre = _this.privilegeGenre[0];
        _this.privilegeGenre = [];
        _this.privilegeGenre.push(genre);
        _this.privilegeGenre = _this.privilegeGenre.concat(_this.fakeGenreData);
      }, 500);
    },
    goBack: function () {
      if (window.history.length > 1) {
        this.$router.go(-1)
      } else {
        this.$router.push('/')
      }
    },
    changeGenre: function (genreId, event) {
      // ジャンル名を切り替え
      if(this.isArticleLoading || genreId == this.currentGenreId){
        return;
      }
      let dom = event.currentTarget;
      this.$refs.genreArea.scrollLeft = dom.offsetLeft - window.innerWidth/2 + dom.offsetWidth/2;
      this.isArticleLoading = true;
      this.currentGenreId = genreId;
      this.$refs.privilegeListPull.setBackScrollPos(0);
      this.$refs.privilegeListArticleList.getArticleListByGenre(genreId);
    },
    refresh: function (loaded) {
      //pull down refresh
      if(this.isArticleLoading){
        loaded("fail");
        return;
      }
      this.$refs.privilegeListArticleList.getArticleListByGenre(this.currentGenreId, loaded);
    },
    setListScrollTop: function () {
      // pulltoの場合、scrolltopを銘記
      this.listScrollTop = this.$refs.privilegeListPull.getScrollPos();
    },
    scrollForLoad: function () {
      // pull up load article list
      if (this.isArticleLoading) {
        return;
      }
      this.isArticleLoading = true;
      let listHeight = this.$refs.privilegeListArticleList.$refs.listBlock.clientHeight;
      let outerHeight = this.$refs.articleListCol.clientHeight;
      let loadingPos = listHeight - outerHeight;
      if (listHeight - outerHeight > 0 && this.$refs.privilegeListPull.getScrollPos() >= loadingPos - 48) {
        this.$refs.privilegeListArticleList.getArticleList();
      } else {
        this.isArticleLoading = false;
      }
    },
    changeLoadingFlag: function(value){
      this.isArticleLoading = value;
    },
    isTopMarkCanShow: function(){
      this.isTopMarkShow = this.$refs.privilegeListPull.getScrollPos() >= 250;
    },
    goTop: function(){
      this.$refs.privilegeListPull.setBackScrollPos(0);
    },
    listenScrollX: function () {
      if (!this.isCanPullDown) {
        return;
      }
      this.isCanPullDown = false;
    },
    reSetScroll: function () {
      if (this.isCanPullDown) {
        return;
      }
      this.isCanPullDown = true;
    },
  }
}
</script>

<style scoped>
.privilegeList .privilegeListHeaderCol{
  height: 42px;
  position: fixed;
  top: 0px;
  width: 100%;
  display: inline-block;
  overflow: hidden;
  vertical-align: top;
}

.privilegeList .privilegeListHeader {
  height: 200px;
  width: 100%;
  display: inline-block;
  overflow: hidden;
  text-align: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.privilegeList .privilegeListHeader .back {
  width: 24px;
  height: 24px;
  display: inline-block;
  position: absolute;
  top: 8px;
  left: 8px;
  line-height: 26px;
  overflow: hidden;
}

.privilegeList .privilegeListHeader .back img {
  width: 7px;
}

.privilegeList .privilegeListHeader .privilegeListTitle {
  display: inline-block;
  position: relative;
  top: 8px;
  height: 24px;
}

.privilegeList .privilegeListHeader .privilegeListTitle .privilegeTitleText {
  font-size: 16px;
  line-height: 21px;
  height: 24px;
  color: var(--white-two);
}

.privilegeList .privilegeListTopArea {
  position: relative;
  height: 78px;
  width: 100%;
  padding-top: 80px;
  overflow: hidden;
}

.privilegeList .privilegeListTopArea .topImgCol{
  position: absolute;
  height: 200px;
  width: 100%;
  top: -42px;
  background-repeat: no-repeat;
  background-size: cover;
}

.privilegeList .privilegeListTop {
  width: 100%;
  text-align: center;
}

.privilegeList .privilegeListTop .privilegeListTitleArea {
  width: 100%;
  position: relative;
}

.privilegeList .privilegeListTop .privilegeListTitleArea .introduceAreaDetail {
  width: calc(100% - 24px);
  max-height: 36px;
  font-size: 12px;
  color: var(--white-two);
  text-align: left;
  margin: auto auto 0 12px;
  line-height: 18px;
  position: relative;
}

.privilegeList .genreCol{
  height: 42px;
  position: relative;
  background: var(--white-four);
}

.privilegeList .genreArea {
  width: 100%;
  height: 42px;
  box-shadow: inset 0 -1px 0 0 var(--white-three);
  background-color: var(--white-four);
  overflow-x: scroll;
}

.privilegeList .genreArea .genreScrollArea{
  min-width: 100%;
  white-space: nowrap;
}

.privilegeList .genreArea .genreScrollArea .genre{
  display: inline-block;
  height: 20px;
  margin-right: 6px;
  text-align: center;
  position: relative;
  padding: 10px 12px 12px 12px;
  color: var(--brown-grey);
}

.privilegeList .genreArea .genreScrollArea .genre.last{
  margin-right: 0px;
}

.privilegeList .genreArea .genreScrollArea .genre.active{
  color: var(--tealish);
}

.privilegeList .genreArea .genreScrollArea .genre.active:after{
  position: absolute;
  content: '';
  width: 100%;
  left: 0;
  bottom: 0px;
  height: 3px;
  background: var(--tealish);
  border-radius: 1.5px;
  transition: left .2s ease;
}

.privilegeList .genreArea .genreScrollArea .genre .genreName{
  font-size: 14px;
  font-weight: 300;
  height: 20px;
}

.privilegeList .privilegeListPullCol {
  margin-top: 42px;
  overflow: hidden;
}

.privilegeList .articleListCol{
  position: relative;
}


</style>
