<script type="text/javascript">
import axios from 'axios'

const LOGIN_PAGE_URL = 'http://192.168.56.102:7010'


var apiDoGet = function (url,param,onSuccess,onFail,onFinally) {

  var requestUrl = process.env.VUE_APP_MEDY_API_ENDPOINT + url;
  requestUrl = requestUrl + param;
  axios.get(requestUrl)
    .then(function (response) {
      // 認証エラーの場合、ログイン画面に遷移する
      if (response.data != null && (response.data.result == "4001" || response.data.result == "4002")) {
        window.location.href = LOGIN_PAGE_URL;
      }
        
      onSuccess(response);
    }).catch(function (error) {
      onFail();
    }).then(function () {
      onFinally();
    });
}

var apiDoPost = function (url,param,onSuccess,onFail,onFinally) {
  var requestUrl = process.env.VUE_APP_MEDY_API_ENDPOINT + url;
  axios.post(requestUrl, param, { headers:{'Content-Type':'applicatoin/json', 'charset':'UTF-8'} })
    .then(function (response) {
      // 認証エラーの場合、ログイン画面に遷移する
      if (response.data != null && (response.data.result == "4001" || response.data.result == "4002")) {
        window.location.href = LOGIN_PAGE_URL;
      }
      
      onSuccess(response);
    }).catch(function (error) {
      onFail();
    }).then(function () {
      onFinally();
    });
}

export default
{
  apiDoGet,
  apiDoPost
}

</script>