<template>
  <div class="articleArea" @click="gotoArticleDetail(article.articleId)">
    <div v-if="!isLifeStyleQA && !isPrivilege">
      <div class="imageArea">
        <img class="articleImage" :src="article.articleImg">
      </div>
      <div class="infoArea">
        <div class="siteName" v-if="isMedyLifeStyle || isFeature">{{article.articleTypeName}}</div>
        <div class="siteName" v-else>{{article.articleSiteName}}</div>
        <div class="title">{{article.articleTitle}}</div>
        <div class="opinionArea">
          <span class="opinion" v-if="article.articleType == 1 && article.commentDisplayFlag != 0">{{article.commentCount}} Opinions</span>
          <span class="opinion" v-if="article.articleType == 1 && article.commentDisplayFlag == 0">閲覧のみ</span>
          <span class="nMinute">{{article.articleStartDate}}</span>
        </div>
      </div>
    </div>
    <div v-if="isLifeStyleQA && !isPrivilege">
      <div class="qaArticleTypeName">{{article.articleTypeName}}</div>
      <div class="qaMark">Q.</div>
      <div class="qaArticleTitle">{{article.articleTitle}}</div>
      <div class="qaArticleBody">{{article.articleBody}}</div>
      <div class="qaArticleBottom">
        <div class="qaOpinions" v-if="article.articleType == 1 && article.commentDisplayFlag != 0">{{article.commentCount}}&nbsp;Opinions</div>
        <div class="qaOpinions" v-if="article.articleType == 1 && article.commentDisplayFlag == 0">閲覧のみ</div>
        <div class="qaArticleStartDate">{{article.articleStartDate}}</div>
      </div>
    </div>
    <div v-if="!isLifeStyleQA && isPrivilege">
      <div class="imageArea">
        <img class="privilegeImage" :src="article.privilegeImg">
      </div>
      <div class="infoArea">
        <div class="privilegeTypeName">{{article.privilegeTypeName}}</div>
        <div class="privilegeLabel">{{article.privilegeLabel}}</div>
        <div class="title">{{article.privilegeTitle}}</div>
        <div class="privilegeDateArea">
          <span class="nMinute">申し込み期限：{{article.privilegeEndDate}}　まで</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "articleCommon",
  props: {
    article: Object,
    isMedyLifeStyle: {
      type: Boolean,
      default: false
    },
    isPrivilege: {
      type: Boolean,
      default: false
    },
    isFeature: {
      type: Boolean,
      default: false
    },
    isLifeStyleQA: {
      type: Boolean,
      default: false
    },
  },
  data() {
    return {
    };
  },
  created: function () {
  },
  mounted: function () {
  },
  activated: function () {
  },
  methods: {
    gotoArticleDetail: function (id) {
      this.$emit('setListScrollTop');
      if(this.isPrivilege){
        //TODO AJAX
        window.open(this.article.privilegeUrl);
      } else if(this.article.articleType == 1){
        //TODO AD
        this.$router.push({ name: 'articleDetail', params: { articleId: id } });
      } else {
        this.$router.push({ name: 'articleDetail', params: { articleId: id } });
      }
    }
  }
};
</script>

<style scoped>
