  margin-left: 12px;
  margin-top: 0px;
  width: calc((100% - 24px));
  box-shadow: inset 0 -1px 0 0 var(--white-four);
  position: relative;
  background-color: var(--white-two);
  display: inline-block;
}

.articleArea .imageArea {
  position: absolute;
  top: 50%;
