  line-height: 16px;
  color: var(--tealish);
  display: inline-block;
  vertical-align: top;
  margin-top: 5px;
  margin-right: 5px;
  position: absolute;
}

.articleArea .qaArticleTitle{
  max-height: 40px;
  font-size: 14px;
  font-weight: 300;
  line-height: 20px;
  color: var(--black);
