<template>
  <div class="lifeStyleQA">
    <div class="lifeStyleQAHeaderCol">
      <div class="lifeStyleQAHeader" :style="'background-image: linear-gradient(to bottom, rgba(51, 51, 51, 0.5), rgba(51, 51, 51, 0.5)),url(' + backGroundImg + ');'">
        <div class="back" @click="goBack()">
          <img src="@/assets/medyImages/icon-navbar-back.png" srcset="@/assets/medyImages/icon-navbar-back@2x.png 2x, @/assets/medyImages/icon-navbar-back@3x.png 3x"/>
        </div>

        <div class="qaTitle">
          <span class="qaText">ライフスタイルQ&amp;A</span>
        </div>

        <div class="search" @click="goSearch()">
          <img src="@/assets/medyImages/search-white.png" srcset="@/assets/medyImages/search-white@2x.png 2x, @/assets/medyImages/search-white@3x.png 3x"/>
        </div>

      </div>
    </div>
    <div class="lifeStyleQAPullCol">
      <pull-to ref="lifeStyleQAPull" @scroll="scrollForLoad" :is-top-bounce="isCanPullDown" :is-bottom-bounce="false" :top-load-method="refresh">
        <div class="lifeStyleQATopArea">
          <div class="topImgCol" :style="'background-image: linear-gradient(to bottom, rgba(51, 51, 51, 0.5), rgba(51, 51, 51, 0.5)),url(' + backGroundImg + ');'"></div>
          <div class="lifeStyleQATop">
            <div class="lifeStyleQATitleArea">
              <div class="introduceAreaTitle">相談・質問してみよう</div>
              <div class="introduceAreaDetail">新生活から仕事の悩みまで他の医者やメディカルに質問したいことを大募集</div>
            </div>
            <div class="questionBtn">
              <span class="question">質問を投稿する</span>
            </div>
          </div>
          <div class="genreCol">
            <div class="genreArea" ref="genreArea" @scroll="listenScrollX" @touchend="reSetScroll">  
              <div class="genreScrollArea">
                <div :class="['genre', index == qaGenre.length - 1? 'last' : '', currentGenreId == genre.genreId ? 'active' : '' ]" :key="index" v-for="(genre, index) in qaGenre" @click="changeGenre(genre.genreId, $event)">
                  <span class="genreName">{{genre.genreName}}</span>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="articleListCol" ref="articleListCol">
          <ArticleList ref="lifeStyleQAArticleList" :menuId="Global.MENU_ID_LIFESTYLE_QA" :isTop="false" @setListScrollTop="setListScrollTop" @changeLoadingFlag="changeLoadingFlag"/>
        </div>
      </pull-to>
    </div>
  <GoTopMark @goTop="goTop" v-if="isTopMarkShow"/>
  </div>
</template>

<script>
import ArticleList from "@/components/cmptsClinical/ArticleList.vue"
import PullTo from "@/components/vue-pull-to/vue-pull-to.vue"
import GoTopMark from "@/components/cmptsCommon/GoTopMark.vue"
import BackGroundImg from "@/assets/medyImages/lifeStyleQA_background.png"

export default {
  name: 'lifeStyleQA',
  components: {
    ArticleList,
    GoTopMark,
    PullTo
  },
  data: function () {
    return {
      currentGenreId: 0,
      qaGenre: [
        {
          genreId: 0,
          genreName: '全て'
        }
      ],
      fakeGenreData: [
        {
          genreId: 1,
          genreName: '循環器'
        },
        {
          genreId: 2,
          genreName: '血液内科'
        },
        {
          genreId: 3,
          genreName: '記事分類3'
        },
        {
          genreId: 4,
          genreName: '記事分類4'
        },
        {
          genreId: 5,
          genreName: '記事分類5'
        },
        {
          genreId: 6,
          genreName: '記事分類6'
        },
        {
          genreId: 7,
          genreName: '記事分類7'
        },
        {
          genreId: 8,
          genreName: '記事分類8'
        },
        {
          genreId: 9,
          genreName: '記事分類9'
        },
        {
          genreId: 10,
          genreName: '記事分類10'
        },
        {
          genreId: 11,
          genreName: '記事分類11'
        },
        {
          genreId: 12,
          genreName: '記事分類12'
        },
        {
          genreId: 13,
          genreName: '記事分類13'
        },
        {
          genreId: 14,
          genreName: '記事分類14'
        },
        {
          genreId: 15,
          genreName: '記事分類15'
        },
        {
          genreId: 16,
          genreName: '記事分類16'
        },
        {
          genreId: 17,
          genreName: '記事分類17'
        }
      ],
      listScrollTop: 0,
      isArticleLoading: false,
      backGroundImg: BackGroundImg,
      isTopMarkShow: false,
      isCanPullDown: true,
      tabId: 7,
    }
  },
  mounted: function () {
    this.init();
  },
  activated: function () {
    if (this.$store.state.isPageBack) {
      // gobackの場合、keep-alliveでcacheを利用する、画面をrefreshしない
      if (this.listScrollTop > 0) {
        this.$refs.lifeStyleQAPull.setBackScrollPos(this.listScrollTop);
        this.listScrollTop = 0;
      }
    } else {
      // 普通遷移の場合、画面初期化処理を行う
      this.init();
    }
  },
  methods: {
    init: function () {
      // pulltoの場合、メインブロックの高さ
      this.$refs.articleListCol.style.height = window.innerHeight - 274 + 'px';
      this.isArticleLoading = false;
      this.currentGenreId = 0;
      this.isTopMarkShow = false;
      this.$refs.lifeStyleQAPull.scrollEl.addEventListener('scroll', this.isTopMarkCanShow);
      this.getGenreData();
      this.Global.currentMenuId = this.Global.MENU_ID_LIFESTYLE_QA;
    },
    getGenreData: function(){
      var _this = this;
      //TODO AJAX GET GENREDATA
      setTimeout(function () {
        let genre = _this.qaGenre[0];
        _this.qaGenre = [];
        _this.qaGenre.push(genre);
        _this.qaGenre = _this.qaGenre.concat(_this.fakeGenreData);
      }, 500);
    },
    goBack: function () {
      if (window.history.length > 1) {
        this.$router.go(-1)
      } else {
        this.$router.push('/')
      }
    },
    changeGenre: function (genreId, event) {
      // ジャンル名を切り替え
      if(this.isArticleLoading || genreId == this.currentGenreId){
        return;
      }
      let dom = event.currentTarget;
      this.$refs.genreArea.scrollLeft = dom.offsetLeft - window.innerWidth/2 + dom.offsetWidth/2;
      this.isArticleLoading = true;
      this.currentGenreId = genreId;
      this.$refs.lifeStyleQAPull.setBackScrollPos(0);
      this.$refs.lifeStyleQAArticleList.getArticleListByGenre(genreId);
    },
    refresh: function (loaded) {
      //pull down refresh
      if(this.isArticleLoading){
        loaded("fail");
        return;
      }
      this.$refs.lifeStyleQAArticleList.getArticleListByGenre(this.currentGenreId, loaded);
    },
    setListScrollTop: function () {
      // pulltoの場合、scrolltopを銘記
      this.listScrollTop = this.$refs.lifeStyleQAPull.getScrollPos();
    },
    goSearch: function () {
      // search pageに遷移する
      this.setListScrollTop();
      this.$router.push({ name: 'search', params: { tabId: this.tabId } });
    },
    scrollForLoad: function () {
      // pull up load article list
      if (this.isArticleLoading) {
        return;
      }
      this.isArticleLoading = true;
      let listHeight = this.$refs.lifeStyleQAArticleList.$refs.listBlock.clientHeight;
      let outerHeight = this.$refs.articleListCol.clientHeight;
      let loadingPos = listHeight - outerHeight;
      if (listHeight - outerHeight > 0 && this.$refs.lifeStyleQAPull.getScrollPos() >= loadingPos - 48) {
        this.$refs.lifeStyleQAArticleList.getArticleList();
      } else {
        this.isArticleLoading = false;
      }
    },
    changeLoadingFlag: function(value){
      this.isArticleLoading = value;
    },
    isTopMarkCanShow: function(){
      this.isTopMarkShow = this.$refs.lifeStyleQAPull.getScrollPos() >= 250;
    },
    goTop: function(){
      this.$refs.lifeStyleQAPull.setBackScrollPos(0);
    },
    listenScrollX: function () {
      if (!this.isCanPullDown) {
        return;
      }
      this.isCanPullDown = false;
    },
    reSetScroll: function () {
      if (this.isCanPullDown) {
        return;
      }
      this.isCanPullDown = true;
    },
  }
}
</script>

<style scoped>
.lifeStyleQA .lifeStyleQAHeaderCol{
  height: 42px;
  position: fixed;
  top: 0px;
  width: 100%;
  display: inline-block;
  overflow: hidden;
  vertical-align: top;
}

.lifeStyleQA .lifeStyleQAHeader {
  height: 200px;
  width: 100%;
  display: inline-block;
  overflow: hidden;
  text-align: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.lifeStyleQA .lifeStyleQAHeader .back {
  width: 24px;
  height: 24px;
  display: inline-block;
  position: absolute;
  top: 8px;
  left: 8px;
  line-height: 26px;
  overflow: hidden;
}

.lifeStyleQA .lifeStyleQAHeader .back img {
  width: 7px;
}

.lifeStyleQA .lifeStyleQAHeader .qaTitle {
  display: inline-block;
  position: relative;
  top: 8px;
  height: 24px;
}

.lifeStyleQA .lifeStyleQAHeader .qaTitle .qaText {
  font-size: 16px;
  line-height: 21px;
  height: 24px;
  color: var(--white-two);
}

.lifeStyleQA .lifeStyleQAHeader .search {
  display: inline-block;
  position: absolute;
  right: 12px;
  top: 9px;
  width: 24px;
  height:24px;
  overflow: hidden;
}

.lifeStyleQA .lifeStyleQAHeader .search img {
  width: 24px;
  position: relative;
  margin-top: -2px;
}

.lifeStyleQA .lifeStyleQATopArea {
  position: relative;
  height: 177px;
  width: 100%;
  padding-top: 55px;
  overflow: hidden;
}

.lifeStyleQA .lifeStyleQATopArea .topImgCol{
  position: absolute;
  height: 200px;
  width: 100%;
  top: -42px;
  background-repeat: no-repeat;
  background-size: cover;
}

.lifeStyleQA .lifeStyleQATop {
  width: 100%;
  text-align: center;
}

.lifeStyleQA .lifeStyleQATop .lifeStyleQATitleArea {
  width: 100%;
  position: relative;
}

.lifeStyleQA .lifeStyleQATop .lifeStyleQATitleArea .introduceAreaTitle {
  width: calc(100% - 30px);
  height: 29px;
  font-size: 20px;
  font-weight: 500;
  line-height: 29px;
  color: var(--white-two);
  text-align: left;
  margin: 0 0 0 15px;
}

.lifeStyleQA .lifeStyleQATop .lifeStyleQATitleArea .introduceAreaDetail {
  width: calc(100% - 31px);
  height: 36px;
  font-size: 12px;
  color: var(--white-two);
  text-align: left;
  margin: 2px 0 0 16px;
}

.lifeStyleQA .questionBtn {
  width: 48.5%;
  height: 20px;
  border-radius: 24px;
  background-color: var(--tealish);
  text-align: center;
  display: inline-block;
  position: relative;
  padding: 13px 0px 15px 0px;
  margin-top: 12px;
  z-index: 1;
}

.lifeStyleQA .questionBtn .question {
  height: 20px;
  font-size: 14px;
  font-weight: 300;
  color: var(--white-two);
  line-height: 20px;
}

.lifeStyleQA .genreCol{
  height: 42px;
  position: relative;
  padding-top: 32px;
  background: var(--white-four);
  margin-top: -24px;
}

.lifeStyleQA .genreArea {
  width: 100%;
  height: 42px;
  box-shadow: inset 0 -1px 0 0 var(--white-three);
  background-color: var(--white-four);
  overflow-x: scroll;
}

.lifeStyleQA .genreArea .genreScrollArea{
  min-width: 100%;
  white-space: nowrap;
}

.lifeStyleQA .genreArea .genreScrollArea .genre{
  display: inline-block;
  height: 20px;
  margin-right: 6px;
  text-align: center;
  position: relative;
  padding: 10px 12px 12px 12px;
  color: var(--brown-grey);
}

.lifeStyleQA .genreArea .genreScrollArea .genre.last{
  margin-right: 0px;
}

.lifeStyleQA .genreArea .genreScrollArea .genre.active{
  color: var(--tealish);
}

.lifeStyleQA .genreArea .genreScrollArea .genre.active:after{
  position: absolute;
  content: '';
  width: 100%;
  left: 0;
  bottom: 0px;
  height: 3px;
  background: var(--tealish);
  border-radius: 1.5px;
  transition: left .2s ease;
}

.lifeStyleQA .genreArea .genreScrollArea .genre .genreName{
  font-size: 14px;
  font-weight: 300;
  height: 20px;
}

.lifeStyleQA .lifeStyleQAPullCol {
  margin-top: 42px;
  overflow: hidden;
}

.lifeStyleQA .articleListCol{
  position: relative;
}


</style>
