<template>
  <div class="pickUpArea">
    <div class="pickUpScrollArea" ref="pickUpScrollArea" @touchstart="getInitPos($event)" @touchmove="moveArticle($event)" @touchend="toggleArticle($event)" @touchcancel="toggleArticle">
      <div class="pickUpInSideArea" ref="pickUpInSideArea" :style="{transform: `translate3d(${diff}px, 0, 0)`}">
        <div class="pickUpArticle" @click="gotoArticleDetail" ref="pickUpArticle" :key="index" v-for="(article, index) in pickUpArticles">
          <div class="pickUpImgArea">
            <img :src="article.articleImg"/>
          </div>
          <div class="pickUpInfoArea">
            <div class="articleTypeName">{{article.articleTypeName}}</div>
            <div class="articleTitle">{{article.articleTitle}}</div>
          </div>
        </div>
      </div>
    </div>
    <div class="toggleArticleArea">
      <div :class="[index == pickUpArticles.length ? 'last' : '', dotIndex == index ? 'active' : '']" @click="nativPickUpArticle(index)" :key="index" v-for="index in pickUpArticles.length"></div>
    </div>
  </div>
</template>

<script>

export default {
  name: 'pickUpArticles',
  props: {
    pickUpArticles: {
      type: Array,
      default: [],
    }
  },
  data: function () {
    return {
      dotIndex: 1,
      touchStartPosX: 0,
      touchStartPosY: 0,
      diff: 0,
      prevDiff: 0,
      isTouchIngY: false,
      isMoving: false,
    }
  },
  components: {
  },
  mounted: function () {
    this.init();
  },
  activated: function () {
    if (this.$store.state.isPageBack) {
      // gobackの場合、keep-alliveでcacheを利用する、画面をrefreshしない
    } else {
      // 普通遷移の場合、画面初期化処理を行う
    }
  },
  watch: {
    diff: function(){
      let winWid = window.innerWidth;
      if(this.diff == 0){
        this.dotIndex = 1;
      } else if(this.diff == -winWid){
        this.dotIndex = 2;
      } else if(this.diff == -winWid*2){
        this.dotIndex = 3;
      }
    }
  },
  methods: {
    init: function () {
      let windowWid = window.innerWidth;
      this.$refs.pickUpInSideArea.style.width = this.pickUpArticles.length * windowWid + 'px'; 
      this.diff = -windowWid * (this.dotIndex - 1);
      this.$refs.pickUpInSideArea.style.transition = 'all .8s ease';
    },
    // pickuparticle
    getInitPos: function(event){
      this.isMoving = true;
      this.touchStartPosX = event.targetTouches[0].pageX;
      this.touchStartPosY = event.targetTouches[0].pageY;
      if(this.prevDiff != 0 && this.prevDiff != window.innerWidth && this.prevDiff != window.innerWidth*2){
          this.prevDiff = -window.innerWidth * (this.dotIndex - 1);
      }
      this.$refs.pickUpInSideArea.style.transition = '';
      if(!this.$store.state.isForbiddenPullDown){
        this.$store.commit('makeScrollForbiddenPullDown');
      }
    },
    moveArticle: function(event){
      if(this.isTouchIngY){
        this.diff = this.prevDiff;
        return;
      }
      if (Math.abs(event.touches[0].pageY - this.touchStartPosY) > 50 && Math.abs(event.touches[0].pageX - this.touchStartPosX) < 50) {
        if(this.$store.state.isForbiddenPullDown){
          this.$store.commit('makeScrollCanPullDown');
        }
        this.diff = this.prevDiff;
        this.isTouchIngY = true;
      } else {
        let widWid = window.innerWidth;
        if(this.prevDiff == 0 && event.touches[0].pageX - this.touchStartPosX > 0){
          this.diff = 0;
        } else if(this.prevDiff == -this.$refs.pickUpScrollArea.clientWidth*(this.pickUpArticles.length - 1) && event.touches[0].pageX - this.touchStartPosX < 0){
          this.diff = this.prevDiff;
        } else if(event.touches[0].pageX - this.touchStartPosX < -widWid){
          this.diff = this.prevDiff - widWid;
        } else if(event.touches[0].pageX - this.touchStartPosX > widWid){
          this.diff = this.prevDiff + widWid;
        }else {
          this.diff = this.prevDiff + event.touches[0].pageX - this.touchStartPosX;
        }
      }
    },
    toggleArticle: function(){
      this.$refs.pickUpInSideArea.style.transition = 'all .8s ease';
      if (!this.$store.state.isForbiddenPullDown) {
        this.isMoving = false;
        return;
      }
      this.$store.commit('makeScrollCanPullDown');
      this.isTouchIngY = false;
      
      if((this.prevDiff == 0 && this.diff < -50) || (this.prevDiff == -this.$refs.pickUpScrollArea.clientWidth*2 && this.diff > this.prevDiff + 50)){
        this.diff = -this.$refs.pickUpScrollArea.clientWidth;
      } else if(this.prevDiff == -this.$refs.pickUpScrollArea.clientWidth && this.diff < this.prevDiff-50){
        this.diff = -this.$refs.pickUpScrollArea.clientWidth * 2;
      } else if(this.prevDiff == -this.$refs.pickUpScrollArea.clientWidth && this.diff > this.prevDiff + 50){
        this.diff = 0;
      } else {
        this.diff = this.prevDiff;
      }
      this.prevDiff = this.diff;
      this.isMoving = false;
    },
    nativPickUpArticle: function(index){
      this.dotIndex = index;
      this.diff = -(index - 1) * this.$refs.pickUpScrollArea.clientWidth;
    },
    gotoArticleDetail: function (id) {
      this.$emit('setListScrollTop');
      this.$router.push({ name: 'articleDetail', params: { articleId: id } });
    }
  }
}
</script>

<style scoped>
.pickUpArea {
  width: 100%;
  height: 240px;
  position: relative;
}

.pickUpArea .pickUpScrollArea{
  width: 100%;
  height: 240px;
  overflow: hidden;
}

.pickUpArea .pickUpScrollArea .pickUpInSideArea{
  height: 240px;
  position: relative;
  min-width: 100%;
}

.pickUpArea .pickUpScrollArea .pickUpInSideArea .pickUpArticle{
  position: relative;
  display: inline-block;
  width: 100vw;
}

.pickUpArea .pickUpScrollArea .pickUpInSideArea .pickUpArticle .pickUpImgArea{
  width: 100%;
  height: 144px;
  overflow: hidden;
  display: inline-block;
  vertical-align: top;
}

.pickUpArea .pickUpScrollArea .pickUpInSideArea .pickUpArticle .pickUpImgArea img{
  width: 100%;
}

.pickUpArea .pickUpScrollArea .pickUpInSideArea .pickUpArticle .pickUpInfoArea{
  width: calc(100% - 24px);
  height: 96px;
  background-color: var(--dark);
  position: relative;
  padding: 0 12px 0 12px;
}

.pickUpArea .pickUpScrollArea .pickUpInSideArea .pickUpArticle .pickUpInfoArea .articleTypeName{
  height: 18px;
  line-height: 18px;
  font-size: 12px;
  font-weight: 300;
  color: var(--white-two);
  display: inline-block;
  margin-top: 8px;
  position: relative;
}

.pickUpArea .pickUpScrollArea .pickUpInSideArea .pickUpArticle .pickUpInfoArea .articleTitle{
  height: 48px;
  font-size: 16px;
  color: var(--white-two);
  line-height: 24px;
  margin-top: 1px;
  display: inline-block;
  position: relative;
  text-overflow: ellipsis;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
  display: -webkit-box;
  overflow: hidden;
  word-break: break-all;
}

.pickUpArea .toggleArticleArea{
  position: absolute;
  display: inline-block;
  bottom: 8px;
  height: 8px;
  left: 50%;
  transform: translateX(-50%);
}

.pickUpArea .toggleArticleArea div{
  width: 6px;
  height: 6px;
  display: inline-block;
  position: relative;
  vertical-align: top;
  border-radius: 50%;
  margin-right: 8px;
  background-color: rgba(51, 51, 51, 0.55)
}

.pickUpArea .toggleArticleArea div.last{
  margin-right: 0px;  
}

.pickUpArea .toggleArticleArea div.active{
  background-color: var(--white-two);
}
</style>
