<script>
import PullTo from "@/components/vue-pull-to/vue-pull-to.vue"
import CommonModal from "@/components/cmptsModal/CommonModal.vue"

export default {
  name: 'inquiry',
  components: {
    PullTo,
    CommonModal,

  },
  data() {
    return {
      isloading: false,
      isModalVisible: false,
      pageDivCss: "",
      name: '山田さん',
      kanaInput: 'ヤマダ',
      mailAddressInput: 'medydemo@gmail.com',
      mailAddressConfirm: 'medydemo@gmail.com',
      jobInput: '医者',
      workplaceInput: '泉ガーデンクリニック',
      subjectInput: '',
      contentInput: '',
      checkEmpty_name: false,
      checkEmpty_kana: false,
      checkNumber_name: false,
      checkNumber_kana: false,
      checkNumber: false,
      checkFormat: false,
      checkMail: false,
      checkConfirm: false,
      isChecked: false,
      modalPattern: 'sendMail',
      inquiryData: [],
    }
  },
  mounted: function () {
    this.pageDivCss = ""
              "width:200px;height:" +
              (window.innerHeight - 42 - 42) +
              "px;margin-top:0px;margin-bottom:0px;  overflow: auto;";
  },
  activated() {
    this.init()
  },
  methods: {
      
    init() {
      this.$refs.listScroll.style.height = window.innerHeight - 42 + 'px';
    },

    handleScroll: function () {

      if (this.isModalVisible) {
        return;
      }
    },
    goBack: function () {
      if (window.history.length > 1) {
        this.$router.go(-1)
      } else {
        this.$router.push('/')
      }
    },
    send: function () {
      //入力チェックを行う。          
      var errorMessage_name = document.getElementById("errorMessage_name")
      var errorMessage_kana = document.getElementById("errorMessage_kana")
      if ((!this.name) && (!this.kanaInput)) {
        this.checkEmpty_name = true
        this.checkEmpty_kana = true
        errorMessage_name.style.display = "block";
        errorMessage_kana.style.display = "block";
      } else if ((!this.name) && (this.kanaInput)) {
        this.checkEmpty_name = true
        this.checkEmpty_kana = false
        errorMessage_name.style.display = "block";
        errorMessage_kana.style.display = "none";
      } else if ((!this.kanaInput) && (this.name)) {
        this.checkEmpty_kana = true
        this.checkEmpty_name = false
        errorMessage_kana.style.display = "block";
        errorMessage_name.style.display = "none";

      } else {
        this.checkEmpty_kana = false
        this.checkEmpty_name = false
        errorMessage_kana.style.display = "none";
        errorMessage_name.style.display = "none";
        this.isChecked = true
      }

    },
    closeModal() {
      this.isChecked = false
      this.$router.push('/')
    },
    refresh: function (loaded) {
      loaded("done");
      return;
    },
  }
}
</script>

