<template>
  <div class="all">
    <div class="word">
      <div class="left" @click="goBack()">{{left}}</div>
      <div class="center">{{center}}</div>
      <div class="right" @click="goBack()">{{right}}</div>
    </div>
    <div class="mailSetCol" ref="mailSetCol">
      <pull-to :top-load-method="refresh">
        <div class="set">
          <p class="setTitle">{{setTitle}}</p>
          <p class="setContent">{{setContent}}</p>
        </div>
        <div class="news">
          <div class="newsPart">
            <div ref="checkOne" class="newsButton" @click="switchOn()" v-if="onClick">
              <div class="buttonWord">{{buttonWord}}</div>
            </div>
            <div class="jageButton" @click="switchOff()" v-if="offClick">
              <div class="jagebuttonNew">
                <div class="jageBuNew">{{jagebutton}}</div>
              </div>
            </div>
            <div class="newsTitle" for="checkOne" v-if="onClick">{{newsTitle}}</div>
            <div class="newsTitle" v-else>{{offTitle}}</div>
          </div>
        </div>
        <div class="activityJudg" ref="activityJudgId" :class="{blueChange:activityJudgNewID}">
          <div class="title">
            <p class="titleWord">{{title}}</p>
          </div>
          <ul class="table">
            <div class="tableDiv">
              <input
                type="radio"
                name="radio"
                id="radioOne"
                class="radioInto"
                :disabled="disabledR1"
                @change="changeRadio()"
                @click="radioOne"
              >
              <div class="choose" for="radioOne">{{choose}}</div>
            </div>
            <div class="tableDiv">
              <input
                type="radio"
                name="radio"
                id="radioTwo"
                class="radioInto"
                :disabled="disabledR2"
                @change="changeRadio()"
                @click="radioTwo"
              >
              <div class="choose" for="radioTwo">{{choosetwo}}</div>
            </div>
            <div class="tableDiv">
              <input
                type="radio"
                name="radio"
                id="radioThree"
                class="radioInto"
                :disabled="disabledR3"
                @change="changeRadio()"
                @click="radioThree"
              >
              <div class="choose" for="radioThree">{{choosethree}}</div>
            </div>
          </ul>
        </div>
        <div class="line"></div>

        <div class="datetime" id="datetimeId" :class="{redChange:dateIdNew}">
          <p class="title" :class="{titleChange:titleChange}">{{titletwo}}</p>
          <ul class="table">
            <div class="dateDiv">
              <input type="checkbox" id="checkboxOne" class="checkboxInto" :disabled="disabledC1">
              <div class="time" for="checkboxOne">{{time}}</div>
            </div>
            <div class="dateDiv">
              <input type="checkbox" id="checkboxTwo" class="checkboxInto" :disabled="disabledC2">
              <div class="time" for="checkboxTwo">{{timeone}}</div>
            </div>
            <div class="dateDiv">
              <input type="checkbox" id="checkboxThree" class="checkboxInto" :disabled="disabledC3">
              <div class="time" for="checkboxThree">{{timetwo}}</div>
            </div>
          </ul>
        </div>
        <div class="line"></div>
        <div class="notice">
          <div class="title">{{titlethree}}</div>
          <div class="content">{{content}}</div>
        </div>
        <div class="jage">
          <div class="jageContent" v-if="inout">{{onContent}}</div>
            <div class="jageContent" v-else>{{jageContent}}</div>
          <div class="nButton" @click="buttonTest" v-if="inout">
            <div class="buWord">{{buttonWord}}</div>
          </div>

          <div class="jButton" @click="buttonTest2" v-if="inoff">
            <div class="jbutton">{{jagebutton}}</div>
          </div>
        </div>
      </pull-to>
    </div>
    <CommonModal :show="isChecked" :patternName="modalPattern" @close="closeModal()"/>
  </div>
</template>
<script>
import CommonModal from "@/components/cmptsModal/CommonModal.vue";
import PullTo from "@/components/vue-pull-to/vue-pull-to.vue";

export default {
  name: "mailset",
  components: {
    CommonModal,
    PullTo
  },
  data() {
    return {
      message: 0,
      isChecked: false,
      modalPattern: "noSelect",
      onClick: true,
      offClick: false,
      inout: true,
      inoff: false,
      left: "キャンセル",
      center: "メルマガ設定",
      right: "完了",
      setTitle: "Myメディカルニュース",
      setContent:
        "Myメディカルニュースで設定した内容に関連する新着医療ニュースをメールでお知らせいたします。",
      newsTitle: "メールマガジンを受け取る",
      offTitle: "メールマガジンを受け取らない",
      title: "配信日選択",
      choose: "毎日",
      choosetwo: "平日のみ",
      choosethree: "週1回(木曜)",
      titletwo: "配信時間帯選択",
      time: "朝(08:00)",
      timeone: "昼(12:00)",
      timetwo: "夜(21:30)",
      titlethree: "キャンペーン・アンケートのお知らせ",
      content:
        "不定期に実施されるキャンペーンやアンケートなどの新着情報をお知らせいたします。",
      jageContent: "お知らせを受け取らない",
      onContent: "お知らせを受け取る",
      jagebutton: "OFF",
      buttonWord: "ON",
      titleChange: false,
      disabledR1: false,
      disabledR2: false,
      disabledR3: false,
      disabledC1: false,
      disabledC2: false,
      disabledC3: false,
      activityJudgNewID: true,
      dateIdNew: true,
    };
  },
  mounted: function() {
    this.init();
  },
  activated: function() {
    if (this.$store.state.isPageBack) {
      // gobackの場合、keep-alliveでcacheを利用する、画面をrefreshしない
    } else {
      // 普通遷移の場合、画面初期化処理を行う
      this.init();
    }
  },
  methods: {
    init: function() {
      this.$refs.mailSetCol.style.height = window.innerHeight - 42 + "px";
    },
    changeRadio: function() {
      document.getElementById("checkboxOne").checked = false;
      document.getElementById("checkboxTwo").checked = false;
      document.getElementById("checkboxThree").checked = false;
      var radio3IsChecked = document.getElementById("radioThree").checked;
      if (radio3IsChecked) {
        this.dateIdNew = false;
        document.getElementById("checkboxOne").disabled = true;
        document.getElementById("checkboxTwo").disabled = true;
        document.getElementById("checkboxThree").disabled = true;
      } else {
        this.dateIdNew = true;
        document.getElementById("checkboxOne").disabled = false;
        document.getElementById("checkboxTwo").disabled = false;
        document.getElementById("checkboxThree").disabled = false;
      }
    },
    switchOn: function() {
      this.onClick = false;
      this.offClick = true;
      this.disabledC1 = true;
      this.disabledC2 = true;
      this.disabledC3 = true;
      this.disabledR1 = true;
      this.disabledR2 = true;
      this.disabledR3 = true;
      this.disabledC = true;
      this.activityJudgNewID = false;
      this.dateIdNew = false;

      // this.isChecked1=false
      // this.isChecked2=false
      // this.isChecked3=false
      // this.isChecked11=false
      // this.isChecked22=false
      // this.isChecked33=false
      document.getElementById("radioOne").checked = false;
      document.getElementById("radioTwo").checked = false;
      document.getElementById("radioThree").checked = false;
      document.getElementById("checkboxOne").checked = false;
      document.getElementById("checkboxTwo").checked = false;
      document.getElementById("checkboxThree").checked = false;
    },
    switchOff() {
      this.onClick = true;
      this.offClick = false;
      this.activityJudgNewID = true;
      this.dateIdNew = true;
      this.disabledR1 = false;
      this.disabledR2 = false;
      this.disabledR3 = false;
      this.disabledC1 = false;
      this.disabledC2 = false;
      this.disabledC3 = false;
      document.getElementById("radioOne").checked = false;
      document.getElementById("radioTwo").checked = false;
      document.getElementById("radioThree").checked = false;
      document.getElementById("checkboxOne").checked = false;
      document.getElementById("checkboxTwo").checked = false;
      document.getElementById("checkboxThree").checked = false;
    },
    radioOne() {
      this.isChecked1 = true;
      this.isChecked11 = false;
      this.isChecked22 = false;
      this.isChecked33 = false;
    },
    radioTwo() {
      this.isChecked2 = true;
      this.isChecked11 = false;
      this.isChecked22 = false;
      this.isChecked33 = false;
    },
    radioThree() {
      this.disabledC1 = true;
      this.disabledC2 = false;
      this.disabledC3 = false;
    },

    buttonTest() {
      (this.inoff = true), (this.inout = false);
    },
    buttonTest2() {
      (this.inoff = false), (this.inout = true);
    },
    goBack() {
      if (window.history.length > 1) {
        this.$router.go(-1);
      } else {
        this.$router.push("/");
      }
    },
  
    closeModal() {
      this.isChecked = false;
    },
    refresh: function(loaded){
      setTimeout(function(){
        loaded('done');
      }, 2000);
    }
  }
};
</script>

