<script type="text/javascript">

const URL = '/api/1/user/get/ranking/article'

var param = {
  appVersion:0,
  rankingType:null,
  menuId:null
}

var getParamStr = function(){
  return "?appVersion=" + param.appVersion 
  + (param.rankingType != null ? ("&rankingType=" + param.rankingType):"" )
  + (param.menuId != null ? ("&menuId=" + param.menuId):"");
}

export default
{
  URL,
  param,
  getParamStr
}

</script>