  components: {
    PickUpArticles,
    ArticleList
  },
  mounted: function () {
    this.init();
  },
  computed: {
    showNoContent: function() {
      return this.questionnaires == null || this.questionnaires.length == 0;
    }
  },
  methods: {
    init: function () {
      this.showAllEnquete = false;
      this.getEnqueteList(null);
    },
    getEnqueteList: function(loaded) {
      var _this = this;
      setTimeout(function () {
        _this.displayEnqueteList = [];
        if (_this.questionnaires == null || _this.questionnaires.length == 0) {
          // コンテンツがありません
        } else if (_this.questionnaires.length <= 4) {
          for (var i=0; i < _this.questionnaires.length; i++) {
            _this.displayEnqueteList.push(_this.questionnaires[i]);
          }
        } else {
          for (var j=0; j < 4; j++) {
            _this.displayEnqueteList.push(_this.questionnaires[j]);
          }
        }
        if (loaded != null) {
          loaded('done');
        }
      }, 2000);
    },
    gotoEnqueteDetail: function (id) {
      this.$router.push({ name: 'enqueteDetail', params: { enqueteId: id }})
    },
    toOver: function () {
      this.$router.push({
        name: 'QuestionOver'
      })
    },
    showMore: function () {
      this.showAllEnquete = true;
      for (var i=4; i < this.questionnaires.length; i++) {
        this.displayEnqueteList.push(this.questionnaires[i]);
      }
    },
    hideMore: function () {
      this.showAllEnquete = false;
      this.displayEnqueteList = [];
      if (this.questionnaires.length <= 4) {
        for (var i=0; i < this.questionnaires.length; i++) {
          this.displayEnqueteList.push(this.questionnaires[i]);
        }
      } else {
        for (var j=0; j < 4; j++) {
          this.displayEnqueteList.push(this.questionnaires[j]);
        }
      }
    },
    refresh: function (loaded) {
      var _this = this;
      setTimeout(function () {
        _this.showAllEnquete = false;
        _this.getEnqueteList(loaded);
        loaded('done');
      }, 2000);
    },
    setListScrollTop: function () {
      // pulltoの場合、scrolltopを銘記
      this.$emit("setListScrollTop");
    },
  }
}
</script>

