<template>
  <div class="privilegeArea">
    <div class="privilegeAreaTop">
      <div class="clickArea" @click="goToPrivilegeList">
        <span>特典・優待</span>
        <img class="arrow" src="@/assets/medyImages/contents-more.png" srcset="@/assets/medyImages/contents-more@2x.png 2x, @/assets/medyImages/contents-more@3x.png 3x">
      </div>
    </div>
    <div class="prompt">{{userName}}様へ特別なご案内がございます。ご確認ください。</div>
    <div class="privilegeBlocks">
      <div :class="['privilegeBlock', item.newStatus == 1 ? 'newPrivilege' : '']" :key="index" v-for="(item, index) in privileges" @click="goPrivilegeDetail(item.privilegeId ,item.privilegeUrl)">
        <div class="newsMark" v-if="item.newStatus == 1"></div>
        <span :class="['privilegeTitle', item.newStatus == 1 ? 'newPrivilege' : '']">{{item.privilegeTitle}}</span>
      </div>
      <div class="privilegeBottom">
        <div @click="goToPrivilegeList">
          <span>もっと見る</span>
          <img class="arrow" src="@/assets/medyImages/contents-more-small.png" srcset="@/assets/medyImages/contents-more-small@2x.png 2x, @/assets/medyImages/contents-more-small@3x.png 3x">
        </div>
      </div>
    </div>
  </div>
</template>

<script>

export default {
  name: 'lifeStyleTopPrivilegeList',
  props: {
    userName: {
      type: String,
      default: ''
    },
    privileges: {
      type: Array,
      default: []
    }
  },
  data: function () {
    return {
    }
  },
  components: {
  },
  mounted: function () {
  },
  activated: function () {
  },
  methods: {
    goToPrivilegeList: function(){
      this.$emit('setListScrollTop');
      this.$router.push({ name: 'privilegeList'});
    },
    goPrivilegeDetail: function(id, url){
      this.privileges.forEach(function(item, index) {
        if(item.privilegeId == id){
          item.newStatus = 0;
        }
      });
      window.open(url);
    }
  }
}
</script>

<style scoped>
.privilegeArea{
  width: 100%;
  display: inline-block;
  position: relative;
  vertical-align: top;
  background-color: var(--white);
  text-align: center;
}
.privilegeArea .privilegeAreaTop {
  padding-top: 20px;
  padding-left: 12px;
  height: 36px;
  position: relative;
  font-size: 18px;
  font-weight: 500;
  color: var(--black);
  text-align: left;
}

.privilegeArea .privilegeAreaTop .clickArea{
  display: inline-block;
}

.privilegeArea .privilegeAreaTop .clickArea span{
  display: inline-block;
  position: relative;
  margin: 4px auto 5px auto;
  height: 27px;
  line-height: 27px;
  vertical-align: top;
}

.privilegeArea .privilegeAreaTop .clickArea .arrow {
  position: relative;
  width: 24px;
  height: 24px;
  margin-left: 8px;
  object-fit: contain;
  margin-top: 6px;
  display: inline-block;
}

.privilegeArea .prompt{
  display: inline-block;
  position: relative;
  width: calc(100% - 90px);
  height: 38px;
  font-size: 13px;
  font-weight: 300;
  line-height: 19px;
  text-align: center;
  color: var(--tealish);
  margin-top: 17px;
}

.privilegeArea .privilegeBlocks{
  width: 100%;
  position: relative;
  margin-top: 12px;
  padding-top: 4px;
}

.privilegeArea .privilegeBlocks .privilegeBlock{
  display: inline-block;
  position: relative;
  width: calc(100% - 59px);
  margin-top: 12px;
  border-radius: 12px;
  background-color: var(--white-four);
  padding: 15px 16px 15px 10px;
  text-align: left;
}

.privilegeArea .privilegeBlocks .privilegeBlock.newPrivilege{
  background-color: var(--white-two);
}

.privilegeArea .privilegeBlocks .privilegeBlock .newsMark{
  display: inline-block;
  width: 9px;
  height: 9px;
  background-color: var(--maize);
  transform: translateY(-50%);
  position: absolute;
  top: 50%;
  border-radius: 50%; 
}

.privilegeArea .privilegeBlocks .privilegeBlock .privilegeTitle{
  margin-left: 19px;
  position: relative;
  display: inline-block;
  max-height: 40px;
  line-height: 20px;
  font-size: 14px;
  font-weight: 300;
  color: var(--brownish-grey);
  text-overflow: ellipsis;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
  display: -webkit-box;
  overflow: hidden;
  max-height: 40px;
  word-break: break-all;
}

.privilegeArea .privilegeBlocks .privilegeBlock .privilegeTitle.newPrivilege{
  color: var(--black);
}

.privilegeArea .privilegeBlocks .privilegeBottom {
  padding-top: 12px;
  height: 50px;
  font-size: 13px;
  font-weight: 300;
  position: relative;
  color: var(--brownish-grey);
  display: inline-block;
}

.privilegeArea .privilegeBlocks .privilegeBottom .arrow {
  margin-top: 2px;
  margin-left: 8px;
  width: 16px;
  height: 16px;
  object-fit: contain;
  vertical-align: top;
}
</style>
