<template>
  <div>
    <MedyHeader :activeMenu.sync="activeMenu" :pageHeaderHide="pageHeaderHide" @setListScrollTop="setListScrollTop"/>
    <SideMenu :isOptinPage="isShowOptinMenu"/>
    <div class="mainArea overFlowCol" :style="mainAreaCss">
      <pull-to ref="topPull" :is-top-bounce="isCanPullDown" :top-load-method="refresh" @scroll="handleScroll">
        <ClinicalTop ref="clinicalTop" :isSecond = "isSecond" @setListScrollTop="setListScrollTop" @changeOptinFlag="changeOptinFlag" v-if="isClinicalTop"/>
        <MedicalNewsTop ref="medicalNewsTop" @setListScrollTop="setListScrollTop" v-if="isMedicalNewsTop"/>
        <EnqueteTop ref="enqueteTop" @setListScrollTop="setListScrollTop" v-if="isEnqueteTop"/>
        <LifeStyleTop ref="lifeStyleTop" @setListScrollTop="setListScrollTop" v-if="isLifeStyleTop"/>
        <FeatureTop ref="featureTop" @setListScrollTop="setListScrollTop" v-if="isFeatureTop"/>
      </pull-to>
    </div>
    <GoTopMark @goTop="goTop" v-if="isTopMarkShow"/>
    <MedyFooter/>
  </div>
</template>

<script>
import MedyHeader from '@/components/MedyHeader.vue'
import ClinicalTop from '@/components/ClinicalTop.vue'
import MedicalNewsTop from '@/components/MedicalNewsTop.vue'
import EnqueteTop from '@/components/QuestionnaireTop.vue'
import LifeStyleTop from '@/components/LifeStyleTop.vue'
import FeatureTop from '@/components/FeatureTop.vue'
import SideMenu from "@/components/SideMenu.vue"
import MedyFooter from '@/components/MedyFooter.vue'
import PullTo from '@/components/vue-pull-to/vue-pull-to.vue'
import GoTopMark from "@/components/cmptsCommon/GoTopMark.vue"

export default {
  name: 'Top',
  data() {
    return {
      mainAreaCss: 'height:100%;margin-top:84px;margin-bottom:46px;',
      isScrolling: false,
      pageHeaderHide: false,
      previousTop: 0,
      listScrollTop: 0,
      isClinicalTop: false,
      isMedicalNewsTop: false,
      isEnqueteTop: false,
      isLifeStyleTop: false,
      isFeatureTop:false,
      activeMenu: this.Global.currentMenuId,
      isOptinPage: true,
      isTopMarkShow: false,
      //TODO DELETE
      isSecond: false
    };
  },
  components: {
    MedyHeader,
    ClinicalTop,
    MedicalNewsTop,
    EnqueteTop,
    LifeStyleTop,
    FeatureTop,
    SideMenu,
    PullTo,
    MedyFooter,
    GoTopMark
  },
  mounted: function () {
  },
  activated: function () {
    this.isClinicalTop = (this.Global.currentMenuId == this.Global.MENU_ID_CLINIC);
    this.isMedicalNewsTop = (this.activeMenu == this.Global.MENU_ID_NEWS);
    this.isEnqueteTop = (this.activeMenu == this.Global.MENU_ID_ENQUETE);
    this.isLifeStyleTop = (this.activeMenu == this.Global.MENU_ID_LIFE);
    this.isFeatureTop = (this.activeMenu == this.Global.MENU_ID_FEATURE);
    if (this.$store.state.isPageBack) {
      // gobackの場合、keep-alliveでcacheを利用する、画面をrefreshしない
      this.autoHideHeader();
      if (this.listScrollTop > 0) {
        this.$refs.topPull.setBackScrollPos(this.listScrollTop);
        this.listScrollTop = 0;
      }
    } else {
      // 普通遷移の場合、画面初期化処理を行う
      this.init();
    }
  },
  computed: {
    isCanPullDown: function () {
      //引き下げるのコントロール
      return !this.$store.state.isForbiddenPullDown;
    },
    isShowOptinMenu: function(){
      //メニュー状態のコントロール
      return this.isOptinPage;
    }
  },
  watch: {
    activeMenu: function(){
      //menuの変更の場合
      this.isClinicalTop = (this.activeMenu == this.Global.MENU_ID_CLINIC);
      this.isMedicalNewsTop = (this.activeMenu == this.Global.MENU_ID_NEWS);
      this.isEnqueteTop = (this.activeMenu == this.Global.MENU_ID_ENQUETE);
      this.isLifeStyleTop = (this.activeMenu == this.Global.MENU_ID_LIFE);
      this.isFeatureTop = (this.activeMenu == this.Global.MENU_ID_FEATURE);
      this.listScrollTop = 0;
      this.$refs.topPull.setBackScrollPos(0);
    }
  },
  methods: {
    init: function () {
      //初期化
      this.listScrollTop = 0;
      this.pageHeaderHide = false;
      this.mainAreaCss = 'height:' + (window.innerHeight - 84 - 46) + 'px;margin-top:84px;margin-bottom:46px;';
      this.isTopMarkShow = false;
      this.$refs.topPull.scrollEl.addEventListener('scroll', this.isTopMarkCanShow);
    },
    handleScroll: function () {
      //headerの表示状態
      if (!this.isScrolling) {
        this.isScrolling = true;
        setTimeout(this.autoHideHeader(), 250);
      }
    },
    autoHideHeader: function () {
      var currentTop = this.$refs.topPull.getScrollPos();
      //headerの表示状態
      if (currentTop <= 300) {
        if(currentTop + 42 <= 350){
          this.mainAreaCss = 'height:' + (window.innerHeight - 84 - 46) + 'px;margin-top:84px;margin-bottom:46px;';
          this.pageHeaderHide = false;
        }
      } else if (currentTop - this.previousTop > 0) {
        if(currentTop - 42 > 350){
          this.mainAreaCss = 'height:' + (window.innerHeight - 42 - 46) + 'px;margin-top:42px;margin-bottom:46px;';
          this.pageHeaderHide = true;
        }
      }
      this.previousTop = currentTop;
      this.isScrolling = false;
    },
    refresh: function (loaded) {
      //TODO AJAX
      if(this.isClinicalTop){
        this.$refs.clinicalTop.refresh(loaded);
      }
      if (this.isMedicalNewsTop){
        this.$refs.medicalNewsTop.refresh(loaded);
      }
      if (this.isEnqueteTop) {
        this.$refs.enqueteTop.refresh(loaded);
      }
      if(this.isLifeStyleTop){
        this.$refs.lifeStyleTop.refresh(loaded);
      }
      setTimeout(function () {
        loaded('done');
      }, 2000)
    },
    setListScrollTop: function () {
      // 遷移の場合、スクロールの場所を保存する
      this.listScrollTop = this.$refs.topPull.getScrollPos();
    },
    //TODO DELETE
    changeOptinFlag: function () {
      this.isOptinPage = false;
      this.isSecond = true;
    },
    isTopMarkCanShow: function(){
      // 上へのボタンを表示かどか
      this.isTopMarkShow = this.$refs.topPull.getScrollPos() >= 250;
    },
    goTop: function(){
      // 上を遷移
      this.$refs.topPull.setBackScrollPos(0);
    }
  }
}
</script>
