<script>

import Cropper from 'cropperjs'
  export default {
    name: 'profielSet',
    components:{
//      croppa
    },
    mounted: function () {
      this.init()
    },
    data() {
      return {
        myImage: require('@/assets/medyImages/avatar.jpg'),
        defaultName: '9文字制限なのかな',
        medyPathName: 'MEDYパスの登録名',
        number: 40,
        showAgeChange: true,
        hideAgeChange: false,
        showServiceChange: true,
        hideServiceChange: false,
        upShow: false,
        downShow: true,
        selectContent: '美容・アンチエイジング',
        depart: [{name: '美容・アンチエイジング'},
                 {name: '美容・アンチエイジング2'},
                 {name: '美容・アンチエイジング3'},
                 {name: '美容・アンチエイジング4'},
                 {name: '美容・アンチエイジング5'},
                ],
        contents: 'プロフィールプロフィールプロフィールプロフィールプロフィールプロフィールプロフィールプロフィールプロフィールプロフィールプロフィールプロフィールプロフィールプロフィールプロフィールプロフィールプロフィールプロフィールプロフィールプロフィール',
        headerImage: require('@/assets/medyImages/avatar.jpg'),
        picValue:'',
        cropper:'',
        croppable:false,
        panel:false,
        url:'',
        openFile: false,
      }
    },
    methods: {
      init() {
        this.$refs.inputText.value = this.defaultName
        this.$refs.proSetScroll.style.height = window.innerHeight - 42 + 'px';
        var self = this;
        var image = document.getElementById('image');
        this.cropper = new Cropper(image, {
          aspectRatio: 1,
          viewMode: 1,
          background:false,
          zoomable:false,
          ready: function () {
            self.croppable = true;
          }
        })
      },
      goBack() {
        if (window.history.length > 1) {
          this.$router.go(-1)
        } else {
          this.$router.push('/')
        }
      },
      submit() {
        if (window.history.length > 1) {
          this.$router.go(-1)
        } else {
          this.$router.push('/')
        }
      },
      showAgeClick() {
        if (!this.showAgeChange) {
          this.showAgeChange = true
          this.hideAgeChange = false
        }
      },
      hideAgeClick() {
        if (!this.hideAgeChange) {
          this.hideAgeChange = true
          this.showAgeChange =false
        }
      },
      showServiceClick() {
        if (!this.showServiceChange) {
          this.showServiceChange = true
          this.hideServiceChange = false
        }
      },
      hideServiceClick() {
        if (!this.hideServiceChange) {
          this.hideServiceChange = true
          this.showServiceChange =false
        }
      },
      checkClick() {
        this.upShow = !this.upShow
        this.downShow = !this.downShow
      },
      listClick(item) {
        this.selectContent = item.name
        this.upShow = !this.upShow
        this.downShow = !this.downShow
      },
      getObjectURL (file) {
        var url = null ; 
        if (window.createObjectURL!=undefined) { // basic
          url = window.createObjectURL(file) ;
        } else if (window.URL!=undefined) { // mozilla(firefox)
          url = window.URL.createObjectURL(file) ;
        } else if (window.webkitURL!=undefined) { // webkit or chrome
          url = window.webkitURL.createObjectURL(file) ;
        }
        return url ;
      },
      change (e) {
        let files = e.target.files || e.dataTransfer.files;
        if (!files.length) return;
        this.panel = true;
        this.picValue = files[0];
        
        this.url = this.getObjectURL(this.picValue);
        if(this.cropper){
          this.cropper.replace(this.url);
        }
        this.panel = true;
   
      },
      crop () {
          this.panel = false;
          var croppedCanvas;
          var roundedCanvas;
   
          if (!this.croppable) {
            return;
          }
          // Crop
          croppedCanvas = this.cropper.getCroppedCanvas();
          console.log(this.cropper)
          // Round
          roundedCanvas = this.getRoundedCanvas(croppedCanvas);
   
          this.headerImage = roundedCanvas.toDataURL();
          this.postImg()
          
      },
      getRoundedCanvas (sourceCanvas) {
        
        var canvas = document.createElement('canvas');
        var context = canvas.getContext('2d');
        var width = sourceCanvas.width;
        var height = sourceCanvas.height;
        
        canvas.width = width;
        canvas.height = height;
   
        context.imageSmoothingEnabled = true;
        context.drawImage(sourceCanvas, 0, 0, width, height);
        context.globalCompositeOperation = 'destination-in';
        context.beginPath();
        context.arc(width / 2, height / 2, Math.min(width, height) / 2, 0, 2 * Math.PI, true);
        context.fill();
   
        return canvas;
      },
      postImg () {

      },
      imageChange() {
        this.$refs.picChange.click()
      }
    },
  }
</script>

