    <div class="breakTimeHeaderCol">
      <div class="breakTimeHeader">
        <div class="back" @click="goBack()">
          <svg xmlns="http://www.w3.org/2000/svg" width="7" height="14" viewBox="0 0 7 14" class="backIcon">
            <g fill="none" fill-rule="evenodd">
              <path d="M-8-5h24v24H-8z"/>
              <path fill="#333" d="M6.566 0a.401.401 0 0 1 .306.137A.474.474 0 0 1 7 .472a.474.474 0 0 1-.128.335l-5.814 6.24 5.738 6.16c.085.09.127.2.127.328a.465.465 0 0 1-.127.328.41.41 0 0 1-.312.137.41.41 0 0 1-.313-.137L.128 7.376A.465.465 0 0 1 0 7.048c0-.128.043-.237.128-.329L6.26.137a.416.416 0 0 1 .147-.103.417.417 0 0 1 .16-.034z"/>
            </g>
          </svg>
        </div>

        <div class="breakTimeTitle">
          <span class="breakTimeText">アンケート結果記事</span>
        </div>
        <div class="search" @click="goSearch()">
          <svg
            class="searchIcon"
            xmlns="http://www.w3.org/2000/svg"
            width="24"
            height="24"
            viewBox="0 0 24 24">
            <path fill="#333" fill-rule="evenodd" d="M20.842 20.033a.537.537 0 0 1 .158.396c0 .158-.053.29-.158.395a.553.553 0 0 1-.403.176.553.553 0 0 1-.403-.176l-5.433-5.484c-.62.539-1.32.96-2.103 1.265a6.8 6.8 0 0 1-2.49.457 6.81 6.81 0 0 1-2.733-.553 7.185 7.185 0 0 1-2.235-1.503 6.91 6.91 0 0 1-1.499-2.233A6.96 6.96 0 0 1 3 10.031c0-.972.181-1.886.543-2.742a6.91 6.91 0 0 1 1.499-2.232 7.185 7.185 0 0 1 2.235-1.503A6.81 6.81 0 0 1 10.01 3c.958 0 1.863.185 2.716.554a7.028 7.028 0 0 1 3.742 3.735c.368.856.552 1.77.552 2.742 0 .856-.146 1.667-.438 2.435a7.304 7.304 0 0 1-1.192 2.065l5.451 5.502zm-10.831-4.078c.806 0 1.568-.155 2.287-.466a5.882 5.882 0 0 0 1.875-1.274 6.11 6.11 0 0 0 1.262-1.881 5.73 5.73 0 0 0 .465-2.303 5.73 5.73 0 0 0-.465-2.302 6.11 6.11 0 0 0-1.262-1.881 5.882 5.882 0 0 0-1.875-1.275 5.704 5.704 0 0 0-2.287-.466c-.818 0-1.586.156-2.305.466A5.882 5.882 0 0 0 5.83 5.848a6.11 6.11 0 0 0-1.262 1.88 5.73 5.73 0 0 0-.465 2.303c0 .82.155 1.588.465 2.303a6.11 6.11 0 0 0 1.262 1.88c.531.54 1.156.965 1.875 1.275.719.31 1.487.466 2.305.466z"/>
          </svg>
        </div>
      </div>
    </div>
    <div class="breakTimePullCol">
      <pull-to ref="breakTimeListPull" @scroll="scrollForLoad" :is-bottom-bounce="false" :top-load-method="refresh">
        <div class="articleListCol" ref="articleListCol">
          <ArticleList ref="articleListbreakTime" :menuId="Global.MENU_ID_ENQUETE" :isTop="false" @setListScrollTop="setListScrollTop" @changeLoadingFlag="changeLoadingFlag"/>
        </div>
      </pull-to>
    </div>
    <GoTopMark @goTop="goTop" v-if="isTopMarkShow"/>
  </div>
</template>

<script>
import ArticleList from "@/components/cmptsClinical/ArticleList.vue"
import PullTo from "@/components/vue-pull-to/vue-pull-to.vue"
import GoTopMark from "@/components/cmptsCommon/GoTopMark.vue"
import BackGroundImg from "@/assets/medyImages/feature_breath_title.jpg"

export default {
  name: 'featureBreakTimeList',
  components: {
    ArticleList,
    GoTopMark,
    PullTo
  },
  data: function () {
    return {
      listScrollTop: 0,
      isArticleLoading: false,
      backGroundImg: BackGroundImg,
      isTopMarkShow: false,
      tabId: 1,
    }
  },
  mounted: function () {
    this.init();
  },
  activated: function () {
    if (this.$store.state.isPageBack) {
      // gobackの場合、keep-alliveでcacheを利用する、画面をrefreshしない
      if (this.listScrollTop > 0) {
        this.$refs.breakTimeListPull.setBackScrollPos(this.listScrollTop);
        this.listScrollTop = 0;
      }
    } else {
      // 普通遷移の場合、画面初期化処理を行う
      this.init();
    }
  },
  methods: {
    init: function () {
      // pulltoの場合、メインブロックの高さ
      this.$refs.articleListCol.style.height = window.innerHeight - 42 + 'px';
      this.isArticleLoading = false;
      this.isTopMarkShow = false;
      this.$refs.breakTimeListPull.scrollEl.addEventListener('scroll',this.isTopMarkCanShow);
    },
    goBack: function () {
      if (window.history.length > 1) {
        this.$router.go(-1)
      } else {
        this.$router.push('/')
      }
    },
    refresh: function (loaded) {
      //pull down refresh
      if (this.isArticleLoading) {
        loaded("fail");
        return;
      }
      this.$refs.articleListbreakTime.getArticleListByGenre(0, loaded);
    },
    setListScrollTop: function () {
      // pulltoの場合、scrolltopを銘記
      this.listScrollTop = this.$refs.breakTimeListPull.getScrollPos();
    },
    goSearch: function () {
      // search pageに遷移する
      this.setListScrollTop();
      this.$router.push({ name: 'search', params: { tabId: 4 } });
    },
    scrollForLoad: function () {
      // pull up load article list
      if (this.isArticleLoading) {
        return;
      }
      this.isArticleLoading = true;
      let listHeight = this.$refs.articleListbreakTime.$refs.listBlock.clientHeight;
      let outerHeight = this.$refs.articleListCol.clientHeight;
      let loadingPos = listHeight - outerHeight;
      if (listHeight - outerHeight > 0 && this.$refs.breakTimeListPull.getScrollPos() >= loadingPos - 48) {
        this.$refs.articleListbreakTime.getArticleList();
      } else {
        this.isArticleLoading = false;
      }
    },
    changeLoadingFlag: function (value) {
      this.isArticleLoading = value;
    },
    // goTopMark function
    isTopMarkCanShow: function(){
      this.isTopMarkShow = this.$refs.breakTimeListPull.getScrollPos() >= 250;
    },
    goTop: function(){
      this.$refs.breakTimeListPull.setBackScrollPos(0);
    }
  }
}
</script>

